namespace $.$$ {
// ============================================================================

export function $nl_defaults_init() {
  ($me_atom2_entity.root() as $me_atom2_entity).props({
    em: () => 16,
    pm: () => 32, // common panel content margin
    colorText: $me_atom2_prop(['.theme'],
      ({masters: [theme]}) => theme == $me_theme.light ? '#313745' : 'white',
    ),
    fontFamily: () => '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"', // https://medium.com/needmore-notes/using-system-fonts-for-web-apps-bf76d214a0e0
    fontWeight: () => 400,
    // : $me_atom2_prop_store({
    //   default: () => false,
    //   valid: (val: any) => typeof val == 'boolean' ? val : null,
    // }),
    // : $me_atom2_prop_store({
    //   default: () => false,
    //   valid: (val: any) => typeof val == 'boolean' ? val : null,
    // }),
    theme: $me_atom2_prop_store({
      default: () => $me_theme.light,
      valid: (val: any) => val == $me_theme.light || val == $me_theme.dark ? val : null,
    }),
    colorButton: $me_atom2_prop(['.theme'],
      ({masters: [theme]}) =>
        theme == $me_theme.light ?
        '#0070a4' :
        '#008ecf',
    ),
    colorLink: $me_atom2_prop(['.theme'],
      ({masters: [theme]}) =>
        theme == $me_theme.light ?
        '#2b87db' :
        '#53adff',
    ),
  })

  $me_atom2_ec.prop_default = <$me_atom2_prop_intf>{
    ...$me_atom2_ec.prop_default,

    em: '/.em',
    pm: '/.pm',
    colorText: '/.colorText',
    fontFamily: '/.fontFamily',
    fontWeight: '/.fontWeight',
    fontSize: '.em',
    theme: '/.theme',
  }

  $me_atom2_elem.style_default = <$me_atom2_prop_intf>{
    ...$me_atom2_elem.style_default,

    color: '.colorText',
    fontFamily: '.fontFamily',
    fontWeight: '.fontWeight',
    fontSize: '.fontSize',
  }
}

// ==========================================================================
}
