namespace $.$$ {
// ============================================================================

export const $me_select = <$me_atom2_elem_cnf_intf>{
  prop: {
  // public:

    options: $me_atom2_prop_abstract<$me_options>(),
    value: $me_atom2_prop_abstract<string|Set<string>>(),

    colorBorder: $me_atom2_prop_abstract<string>(),
    colorBorderSelected: $me_atom2_prop_abstract<string>(),

    colorText: $me_atom2_prop_abstract<string>(),
    colorTextSelected: $me_atom2_prop_abstract<string>(),

    colorBackground: $me_atom2_prop_abstract<string>(),
    colorBackgroundSelected: $me_atom2_prop_abstract<string>(),

    borderRadius: $me_atom2_prop_abstract(),
    borderWidth: $me_atom2_prop_abstract(),
    paddingHor: $me_atom2_prop_abstract(),

    '#width': $me_atom2_prop_abstract(),
    '#height': $me_atom2_prop_abstract(),
    option_width_min: $me_atom2_prop_abstract(),

    fontFamily: $me_atom2_prop_abstract<string>(),
    fontWeight: $me_atom2_prop_abstract(),
    fontWeightSelected: $me_atom2_prop_abstract(),
    fontSize: $me_atom2_prop_abstract(),

    alignVer: () => $me_align.center,
    ofsVer: () => 0,
    no_adjust: () => false,

  // internal:

    option_ids: $me_atom2_prop_keys(['.options']),
    _option_width: $me_atom2_prop<{[id: string]: number}>(
      $me_atom2_prop_masters(['.option_ids'],
        ({masters: [option_ids]}) => {
          const result = ['.no_adjust', '.option_ids', '.options', '.value', '.#width', '.option_width_min']
          for (const id of option_ids)
            result.push(`^option[${id}]._textWidth`, `^option[${id}].paddingLeft`, `^option[${id}].paddingRight`)
          return result
        },
      ),
      ({masters, prev}) => {
        const [no_adjust, option_ids, options, value, width_total, option_width_min] = masters
        if (no_adjust && prev) return prev
        const masters_base = 6
        const width: {[id: string]: number} = {}
        let width_sum = 0
        let width_excess_base = 0
        const ids = [] as string[]
        let w: number
        for (let i = 0; i < option_ids.length; i++) {
          const id = option_ids[i]
          const caption = $me_option_caption(id, options, value)
          const isSpecifiedWidth =
            typeof caption != 'string' &&
            caption &&
            typeof (caption as $me_options_caption_intf).width == 'number'
          const w = width[id] =
            isSpecifiedWidth ?
              (caption as $me_options_caption_intf).width :
              Math.max(option_width_min,
                masters[masters_base + i * 3] + masters[masters_base + 1 + i * 3] + masters[masters_base + 2 + i * 3]
              )
          width_sum += w

          if (!isSpecifiedWidth) {
            width_excess_base += w
            ids.push(id)
          }
        }
        const width_excess = width_total - width_sum
        for (const id of ids)
          width[id] *= (1 + width_excess / width_excess_base)
        return width
      },
    ),
    option_width: $me_atom2_prop({ keys: ['.option_ids'], masters: ['._option_width'] },
      ({key: [id], masters: [width]}) => width[id],
    ),
    option_left: $me_atom2_prop(
      {
        keys: ['.option_ids'],
        masters: $me_atom2_prop_masters(['.option_ids'],
          ({key: [id], masters: [ids]}) => {
            const idx = ids.indexOf(id)
            const result = [] as string[]
            if (!idx) return result
            for (let i = 0; i < idx; i++)
              result.push(`.option_width[${ids[i]}]`)
            return result
          }
        ),
      },
      $me_atom2_prop_compute_fn_sum(),
    ),
    idx_selected: $me_atom2_prop(['.option_ids', '.value'],
      ({masters: [ids, id]}) =>
        typeof id == 'string' ? [ids.indexOf(id)].filter(idx => ~idx) :
        id instanceof Set ? [...(id as Set<string>)].map((id: string) => ids.indexOf(id)).filter(idx => ~idx) :
        [],
    ),
  },
  event: {
  },
  control: {

    option: $me_atom2_prop<$me_atom2_control_cnf_intf>(
      {keys: ['.option_ids']},
      ({key: [id]}) => <$me_atom2_control_cnf_intf>{
        base: $me_label,
        prop: {
          fontSize: '<.fontSize',
          fontWeight: $me_atom2_prop(['.isSelected', '<.fontWeightSelected', '<.fontWeight'],
            ({masters: [isSelected, fontWeightSelected, fontWeight]}) => isSelected ? fontWeightSelected : fontWeight,
          ),
          fontFamily: '<.fontFamily',
          isSelected: $me_atom2_prop(['<.value'],
            ({masters: [value]}) => value instanceof Set ? value.has(id) : value == id,
          ),
          paddingHor: '<.paddingHor',
          text: $me_atom2_prop(['<.options', '<.value'],
            ({masters: [options, value]}) =>
              $me_option_caption_text(id, options, value)
          ),
          alignHor: () => $me_align.center,
          alignVer: '<.alignVer',
          ofsVer: '<.ofsVer',
          '#width': $me_atom2_prop([`<.option_width[${id}]`],
            ({masters: [to]}) => $me_atom2_anim({to}),
          ),
          '#height': '<.#height',
          '#ofsHor': $me_atom2_prop([`<.option_left[${id}]`],
            ({masters: [to]}) =>
            $me_atom2_anim({to}),
          ),
          idx: $me_atom2_prop(['<.option_ids'],
            ({masters: [ids]}) => ids.indexOf(id),
          ),
          _ctxLeft: $me_atom2_prop(
            $me_atom2_prop_masters(['<.no_adjust'],
              ({masters: [no_adjust]}) =>
                no_adjust ?
                ['._text_n_ctxLeft'] :
                ['.#ctx', '.text', '.period', '/.#pixelRatio', `<.option_width[${id}]`, `<.option_left[${id}]`, '.paddingLeft', '.paddingRight', '.alignHor']
            ),
            ({len, masters}) => {
              if (len == 1) {
                return masters[0].ctxLeft
              } else {
                const [ctx, text, period, pixelRatio, width, left, paddingLeft, paddingRight, alignHor] = masters
                return $me_atom2_anim({to:
                  $me_label_text_n_ctxLeft({ctx, text, period, pixelRatio, width, left, paddingLeft, paddingRight, alignHor}).ctxLeft
                })
              }
            }
          ),
          borderWidthVer: '<.borderWidth',
          colorBorderVer: $me_atom2_prop(['.isSelected', '<.colorBorder', '<.colorBorderSelected'],
            ({masters: [isSelected, colorBorder, colorBorderSelected]}) => isSelected ? colorBorderSelected : colorBorder
          ),
          borderLeft: $me_atom2_prop<{width: number, color?: string}>(['<.idx_selected', '<.option_ids', '<.colorBorder', '<.colorBorderSelected', '<.borderWidth'],
            ({masters: [idx_selected, option_ids, colorBorder, colorBorderSelected, borderWidth]}) => {
              const idx = option_ids.indexOf(id)
              const result =
                ~idx_selected.indexOf(idx) ?
                  (~idx_selected.indexOf(idx - 1) ? {width: borderWidth, color: 'transparent'} : {width: borderWidth, color: colorBorderSelected}) :
                !idx ? {width: borderWidth, color: colorBorder} :
                !idx ? {width: borderWidth, color: colorBorder} : {width: borderWidth, color: 'transparent'}
              return result
            },
          ),
          colorBorderLeft: $me_atom2_prop(['.borderLeft'],
            ({masters: [border]}) => border.color || 'transparent',
          ),
          borderWidthLeft: $me_atom2_prop(['.borderLeft'],
            ({masters: [border]}) => border.width || 0,
          ),
          borderRight: $me_atom2_prop<{width: number, color?: string}>(['<.idx_selected', '<.option_ids', '<.colorBorder', '<.colorBorderSelected', '<.borderWidth'],
            ({masters: [idx_selected, option_ids, colorBorder, colorBorderSelected, borderWidth]}) => {
              const idx = option_ids.indexOf(id)
              const result =
                ~idx_selected.indexOf(idx) ? {width: borderWidth, color: colorBorderSelected} :
                idx == option_ids.length - 1 || !~idx_selected.indexOf(idx + 1) ? {width: borderWidth, color: colorBorder} :
                {width: borderWidth, color: 'transparent'}
              return result
            },
          ),
          colorBorderRight: $me_atom2_prop(['.borderRight'],
            ({masters: [border]}) => border.color || 'transparent',
          ),
          borderWidthRight: $me_atom2_prop(['.borderRight'],
            ({masters: [border]}) => border.width || 0,
          ),
          borderRadiusLeftTop: $me_atom2_prop(['.idx', '<.borderRadius'],
            ({masters: [idx, borderRadius]}) => idx ? 0 : borderRadius,
          ),
          borderRadiusLeftBottom: $me_atom2_prop(['.idx', '<.borderRadius'],
            ({masters: [idx, borderRadius]}) => idx ? 0 : borderRadius,
          ),
          borderRadiusRightTop: $me_atom2_prop(['.idx', '<.option_ids', '<.borderRadius'],
            ({masters: [idx, option_ids, borderRadius]}) => idx != option_ids.length - 1 ? 0 : borderRadius,
          ),
          borderRadiusRightBottom: $me_atom2_prop(['.idx', '<.option_ids', '<.borderRadius'],
            ({masters: [idx, option_ids, borderRadius]}) => idx != option_ids.length - 1 ? 0 : borderRadius,
          ),
          colorBackground: $me_atom2_prop(['.isSelected', '<.colorBackgroundSelected', '<.colorBackground'],
            ({masters: [isSelected, colorBackgroundSelected, colorBackground]}) => isSelected ? colorBackgroundSelected : colorBackground,
          ),
          colorText: $me_atom2_prop(['.isSelected', '<.colorTextSelected', '<.colorText'],
            ({masters: [isSelected, colorTextSelected, colorText]}) => isSelected ? colorTextSelected : colorText,
          ),
          '#zIndex': $me_atom2_prop(['<.#zIndex'],
            ({masters: [zIndex]}) => zIndex + 1,
          ),
        },
        prop_non_render: {
          '#cursor': $me_atom2_prop<string>(['<.value'],
            ({masters: [value]}) => value instanceof Set || value != id ? 'pointer' : null,
          ),
        },
        event: {
          clickOrTap: () => {
            let value = a<string|Set<string>>('<.value')
            if (typeof value == 'string' && <string>value == id) return false
            if (value instanceof Set) {
              if ((<Set<string>>value).has(id)) {
                (<Set<string>>value).delete(id)
              } else {
                (<Set<string>>value).add(id)
              }
            } else value = id
            a<any>('<.value', value, true)
            return true
          },
        },
      },
    ),

  },
}

// ==========================================================================
}
