namespace $.$$ {
// ============================================================================

export const $me_list2_demo = (rootElem: HTMLElement) => {
  $nl_defaults_init()

  return new $me_atom2_elem({tail: 'app', cnf: {
    node: rootElem,
    style: {
      margin: () => 0,
    },
    prop: {
      demo_border: () => 'red',
    },
    elem: {
      picker: () => <$me_atom2_elem_cnf_intf>{
        base: $nl_picker,
        prop: {
          options: () => <$me_options>{
            '$me_list2_demo_metro': {
              caption: '',
            },
            '$me_list2_demo_grid': {
              caption: 'Grid',
            },
          },
          value: $me_atom2_prop_store({
            default: () =>
              Object.keys(a<$me_options>('.options'))[0],
            valid: (val: any) =>
              a<$me_options>('.options').hasOwnProperty(val) ? val : null,
          }),
          // value: () => '$me_list2_demo_metro',
          '#height': () => 32,
          '#width': () => 218,
          '#ofsVer': () => 16,
          '#ofsHor': () => 16,
        },
      },
      demo: $me_atom2_prop<$me_atom2_elem_cnf_intf>(['@picker.value'],
        ({masters: [pickerValue]}) => <$me_atom2_elem_cnf_intf>{
          type: pickerValue,
          base: $.$$[pickerValue],
          prop: {
            marginVer: () => 16,
            '#height': $me_atom2_prop(['/.#viewportHeight', '.marginVer', '<@picker.#ofsVer', '<@picker.#height'],
              ({masters: [viewportHeight, marginVer, pickerOfsVer, pickerHeight]}) =>
                viewportHeight - pickerOfsVer - pickerHeight - 2 * marginVer,
            ),
            '#ofsVer': $me_atom2_prop(['<@picker.#ofsVer', '<@picker.#height', '.marginVer'],
              // ({masters: [ofsVer, height]}) => .5*(ofsVer + height),
              $me_atom2_prop_compute_fn_sum(),
            ),
          },
        },
      ),
    }
  }})
}

// ==========================================================================
}