namespace $.$$ {
// ============================================================================

let _dataWorker: Worker

export const $nl_search_panel_result = <$me_atom2_elem_cnf_intf>{
  base: $nl_search_panel,
  dispatch: (dispatch_name: string, dispatch_arg?: any) => {
    if (dispatch_name == 'get_recs') {
    // if (dispatch_name == 'get_row_height') {
    //   const row_heights = a<Map<number, number>>('.row_heights')
    //   dispatch_arg.val = !row_heights.has(dispatch_arg.idx) ? a('@grid.row_height_min') : row_heights.get(dispatch_arg.idx)
    //   return true
    // } else if (dispatch_name == 'set_row_height') {
    //   const row_heights = a<Map<number, number>>('.row_heights')
    //   if (dispatch_arg.val != a('@grid.row_height_min')) {
    //     row_heights.set(dispatch_arg.idx, dispatch_arg.val)
    //   } else if (row_heights.has(dispatch_arg.idx)) {
    //     row_heights.delete(dispatch_arg.idx)
    //   }
    //   return true
    // } else
    // {
      // console.log(dispatch_arg)
      a<Worker>('.dataWorker').postMessage({
        ...dispatch_arg,
        cmd: 'recs',
        tag: a('.provider_tag')
      })
      return true
    } else if (dispatch_name == 'get_card') {
      a<Worker>('.dataWorker').postMessage({
        ...dispatch_arg,
        cmd: 'card',
        tag: a('.provider_tag')
      })
      return true
    }  else if (dispatch_name == 'recs') {
      // console.warn(dispatch_name, dispatch_arg)
      // const ec = a.get('@grid') as $me_atom2_ec
      // ec.dispatch(dispatch_name, dispatch_arg)
      a.dispatch('@grid@list', dispatch_name, dispatch_arg)
      return true
    }  else if (dispatch_name == 'card') {
      if (dispatch_arg.item) {
        a.update('/@app.card_value', val => { return dispatch_arg.item })
        a<boolean>('/@app.isShownCard', true)
      } else {
        console.warn('cannot read card data', dispatch_arg)
      }
      return true
    }
    return false
  },
  prop: {
    _provider: () => a.curr.parent(true).path,
    provider_tag: () => null as string,
    on_order_changed: $me_atom2_prop(['.order'], null,
      ({val}) => {
        a('.provider_tag', val.id)
      },
    ),
    isZoomed: '/@app.isZoomed',

    // isZoomed: $me_atom2_prop_store({
    //   default: () => false,
    //   valid: (val: any) => typeof val == 'boolean' ? val : false,
    // }),

    // () => false,
    // TODO: use this instead of masters.mode, but before: for elem '#_hidden': ['<.#hidden', '.#hidden'], $me_atom2_prop_compute_fn_and, and optimization for "and"
    // mode: $me_atom2_prop<string>([`.order`],
    //   ({masters: [order]}) => order.result_mode || '',
    //   ({val}) => {
    //     const order = a<any>(`.order`)
    //     order.result_mode = val
    //     a<any>(`.order`, order, true)
    //   },
    // ),
    count: $me_atom2_prop(['.provider_tag'],
      ({masters: [tag]}) => {
        a<Worker>('.dataWorker').postMessage({cmd: 'count', tag})
        return -1
      },
      // ({val}) => console.log(val)
    ),
    dataWorker: () => {
      if (!_dataWorker) {
        // console.log(window.location.href)
        const base = window.location.href.slice(0, -window.location.search.length)
        _dataWorker = new Worker(base + 'data/-/web.js')
        const curr = a.curr
        _dataWorker.onmessage = (event: any) => {
          const prev = a.curr
          a.curr = curr
          if (event.data.tag != a('.provider_tag')) {
            console.warn({tag: event.data.tag, provider_tag: a('.provider_tag')}, event.data)
          } else if (event.data.status != 'ok') {
            console.error(event.data)
          } else if (event.data.cmd == 'count') {
            // console.log(event.data.count)
            a('.count', event.data.count)
          } else if (event.data.cmd == 'recs') {
            a.dispatch('', event.data.cmd, event.data)
            // const ec = a.curr.parent(true) as $me_atom2_ec
            // console.log(event.data, a('.provider_tag'))
            // const ec = a.get('@grid') as $me_atom2_ec
            // ec.dispatch(event.data.cmd, event.data)
            // console.log(a.get('@grid'))
          } else if (event.data.cmd == 'card') {
            a.dispatch('', event.data.cmd, event.data)
          } else {
            console.error(event.data)
          }
          a.curr = prev
        }
      }
      return _dataWorker
    },
    // '#zIndex'

  },
  event: {
    clickOrTap: () => true,
  },
  style: {
    userSelect: () => 'none',
  },
  elem: {

    resizer: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#width': () => 44,
        '#height': () => 44,
        '#zIndex': $me_atom2_prop(['<.#zIndex'],
          ({masters: [zIndex]}) => zIndex + 3,
        ),
        '#cursor': () => 'pointer',
      },
      elem: {
        img: () => <$me_atom2_elem_cnf_intf>{
          node: 'img',
          prop: {
            '#height': $me_atom2_prop(['<<.isZoomed'],
              ({masters: [isZoomed]}) => isZoomed ? 68 : 34,
            ),
            '#width': $me_atom2_prop(['<<.isZoomed'],
              ({masters: [isZoomed]}) => isZoomed ? 68 : 34,
            ),
            // '#height': () => 34,
            // '#width': () => 34,
            '#ofsVer': () => -2,
            '#ofsHor': () => -4,
          },
          attr: {
            src: $me_atom2_prop(['/.theme', '<<.isZoomed'],
              ({masters: [theme, isZoomed]}) => `assets/${$me_theme[theme]}-slide-${isZoomed ? 'downright' : 'upleft'}@2x.png`
            ),
          },
          style: {
            userSelect: () => 'none',
          },
        },
      },
      event: {
        clickOrTap: () => {
          a<boolean>('/@app.isZoomed', !a<boolean>('/@app.isZoomed'))
          // console.log('resizer')
          return true
        },
      },
    },

    shown: () => <$me_atom2_elem_cnf_intf>{
      node: 'span',
      prop: {
        '#height': () => null as number,
        '#width': () => null as number,
        // '#ofsHor': '.em',
        '#ofsHor': $me_atom2_prop(['.em', '<.isZoomed'],
          ({masters: [ofs, isZoomed]}) => isZoomed ? 3*ofs : ofs,
        ),
        '#ofsVer': $me_atom2_prop(['.em'],
          $me_atom2_prop_compute_fn_mul(19 / 16),
        ),
      },
      style: {
        position: () => 'relative',
        fontWeight: () => 500,
        userSelect: () => 'none',
      },
      dom: {
        innerText: $me_atom2_prop(['<.count'],
          ({masters: [count]}) =>
            (
              count < 0 ?
              ` ` :
              ` ${count} ` + $me_word_plural(count, '', '', '')
            ).toUpperCase()
        ),
      },
    },

    filter: () => <$me_atom2_elem_cnf_intf>{
      node: 'img',
      prop: {
        '#height': () => 18,
        '#width': () => 18,
        '#ofsVer': () => 18,
        '#ofsHor': $me_atom2_prop(['<@shown.#ofsHor', '<@shown.#width'],
          $me_atom2_prop_compute_fn_sum(8),
        ),
        '#cursor': () => 'pointer',
      },
      attr: {
        src: () => 'assets/icons-8-filter@2x.png'
      },
      style: {
        userSelect: () => 'none',
        filter: $me_atom2_prop(['/.theme'],
          ({masters: [theme]}) =>
            theme == $me_theme.light ?
              // black => #0070a4  by codepen.io/sosuke/pen/Pjoqqp
              'brightness(0%) invert(22%) sepia(56%) saturate(3987%) hue-rotate(182deg) brightness(96%) contrast(101%)' :
              // black => #0facf4  by codepen.io/sosuke/pen/Pjoqqp
              'brightness(0%) invert(45%) sepia(90%) saturate(515%) hue-rotate(154deg) brightness(106%) contrast(97%)'
        ),
      },
      event: {
        clickOrTap: () => {
          console.log('filter')
          return true
        },
      },
    },

    mode_switcher: () => <$me_atom2_elem_cnf_intf>{
      base: $nl_switch,
      prop: {
        '#alignHor': () => $me_align.right,
        '#width': () => 265,
        '#height': () => 40,
        ofsVer: () => 5,
        options: () => ({
          '': {},
          '': {},
          '': {},
        }),
        value: $me_atom2_prop<string>([`<.mode`],
          null,
          ({val}) => {
            a<string>('<.mode', val)
          },
        ),
        paddingHor: () => 16,
      },
    },

    spinner: $me_atom2_prop<$me_atom2_elem_cnf_intf>(['.count'],
      ({masters: [count]}) => count >= 0 ? null : <$me_atom2_elem_cnf_intf>{
        base: $me_spinner,
        prop: {
          color: () => '#D9DCE2',
        },
      },
    ),

    grid:
    // $me_atom2_prop<$me_atom2_elem_cnf_intf>(['.mode'],
      // ({masters: [mode], prev}) => mode != '' ? prev || null :
      () => <$me_atom2_elem_cnf_intf>{
        // type: '$nl_search_grid',
        // base: $nl_search_grid,
        base: grid,
        prop: {
          // '#hidden': $me_atom2_prop<boolean>(['<.mode'],
          //   ({masters: [mode]}) => mode != ''
          // ),
          provider: () => (a.get('<') as $me_atom2_ec).path,
          order: $me_atom2_prop(['<.order'],
            null,
            ({val}) => {
              a('<.order', val, true)
            }
          ),

          '#hidden': $me_atom2_prop(['<.mode'],
            ({masters: [mode]}) => mode != '',
          ),
          '#ofsHor': '.em',
          '#ofsVer': () => 56,
          '#width': $me_atom2_prop(['<.#width', '.#ofsHor'],
            ({masters: [width, ofsHor]}) => width - 2 * ofsHor,
          ),
          '#height': $me_atom2_prop(['<.height_target', '<.height_anim_is', '.#ofsVer', '.em'],
            ({masters: [height, height_anim_is, ofsVer, em], prev}) => {
              const val = height - ofsVer - em
              const result = prev == null || val > prev || !height_anim_is ? val : prev
              return result
            }
          ),

          provider_tag: () => '',
          rec_count: '<.count',
        },
      },
    // ),

    plitka:
      () => <$me_atom2_elem_cnf_intf>{
        base: plitka,
        prop: {
          provider: () => (a.get('<') as $me_atom2_ec).path,
          provider_tag: () => 'some',

          rec_count: '<.count',

          order: $me_atom2_prop(['<.order'],
            null,
            ({val}) => {
              a('<.order', val, true)
            }
          ),
          '#hidden': $me_atom2_prop(['<.mode'],
            ({masters: [mode]}) => mode != '',
          ),
          '#ofsHor': '.em',
          '#ofsVer': () => 56,
          '#width': $me_atom2_prop(['<.#width', '.#ofsHor'],
            ({masters: [width, ofsHor]}) => width - 2 * ofsHor,
          ),
          '#height': $me_atom2_prop(['<.height_target', '<.height_anim_is', '.#ofsVer'],
            ({masters: [height, height_anim_is, ofsVer], prev}) => {
              const val = height - ofsVer
              const result = prev == null || val > prev || !height_anim_is ? val : prev
              return result
            }
          ),
        },
      },
  },
}

const plitka  = <$me_atom2_elem_cnf_intf>{
  base: $nl_plitka,
}
// console.log($nl_search_panel_result_plitka)

const grid = <$me_atom2_elem_cnf_intf>{
  base: $nl_search_grid,
  prop: {
    row_opens: $me_atom2_prop(['.order'],
      ({masters: [order]}) => order.row_opens || (order.row_opens = new Set<number>()),
    ),
    on_order_changed: $me_atom2_prop(['.order'],
      null,
      // ({masters: [order , rec_count]}) =>
        // rec_count < 0 ? null : [order, rec_count],
      ({val}) => {
        if (!val) return
        const order = val
        // if (!order.cols) order.cols = Object.keys(a('.cols'))
        a.dispatch('', 'set_view', order)
      },
    ),
    on_order_changed_and_ready: $me_atom2_prop(['.order', '.rec_count'],
      ({masters: [order , rec_count]}) =>
        rec_count < 0 ? null : [order, rec_count],
      ({val}) => {
        if (!val) return
        const [order, rec_count] = val
        // if (!order.cols) order.cols = Object.keys(a('.cols'))
        a.dispatch('@list', 'set_view', order)
      },
    ),
    on_change_col_ids: $me_atom2_prop(['.col_ids', '.order'], null,
      ({val: [col_ids, order]}) => {
        order.col_ids = col_ids
      }
    ),
    on_change_ofsHor: $me_atom2_prop(['.ofsHor', '.col_fixed_width','.order'], null,
      ({val: [ofsHor, col_fixed_width, order]}) => {
        order.ofsHor = ofsHor - col_fixed_width
        // console.log(order.ofsHor, ofsHor)
      }
    ),
    on_change_col_width: $me_atom2_prop({keys: ['.col_ids'], masters: ['.col_width[]', '.cols', '.order']},
      null,
      ({key: [id], val}) => {
        if (val == null) return
        const [width, cols, order] = val
        if (width != cols[id].width) {
          if (!order.col_width) order.col_width = {} as any
          order.col_width[id] = width
        }
      },
    ),
    on_change_row_i_min: $me_atom2_prop(['@list.row_i_min', '.order', '.rec_count'], null,
      ({val: [row_i_min, order, rec_count]}) => {
        if (rec_count < 0) return
        order.row_i_min = row_i_min
      }
    ),
    on_change_visible_idx_min: $me_atom2_prop(['@list.visible_idx_min', '.order', '.rec_count'], null,
      ({val: [visible_idx_min, order, rec_count]}) => {
        if (rec_count < 0) return
        order.visible_idx_min = visible_idx_min
      }
    ),
    on_change_visible_top: $me_atom2_prop(['@list.visible_top', '.order', '.rec_count'], null,
      ({val: [visible_top, order, rec_count]}) => {
        if (rec_count < 0) return
        order.visible_top = visible_top
      }
    ),
    cols: () => $nl_search_panel_result_cols,
  },
}

// ==========================================================================
}