namespace $.$$ {
// ============================================================================

export interface $me_ribbon_effect_p {
  id: string,
  init?: $me_fn<void>,
  fini?: $me_fn<void>,
  from: number | string,
  to: number | string,
  adjust: string,
  delta: number,
  fromBack: boolean,
}

export function $me_ribbon_effect(p: $me_ribbon_effect_p) {
  const flush = () => {
    delete effect_timers[p.id]
    locks[p.id] = true
    if (p.init) p.init()

    const prop_adjust = a.get(p.adjust) as $me_atom2

    let prop_from: $me_atom2
    if (typeof p.from != 'number')
      prop_from = a.get(p.from) as $me_atom2

    let prop_to: $me_atom2
    if (typeof p.to != 'number')
      prop_to = a.get(p.to) as $me_atom2

    if (
      prop_adjust instanceof $me_atom2 &&
      (typeof p.from == 'number' || prop_from instanceof $me_atom2) &&
      (typeof p.to == 'number' || prop_to instanceof $me_atom2)
    ) {
      prop_adjust.value(
        $me_atom2_anim<number>({
          from: !prop_from ? p.from as number : prop_from.value(),
          to: !prop_to ? p.to as number : prop_to.value(),
          fini: p.fini,
        })
      )
    // } else {
    //   console.error(prop_adjust, prop_from, prop_to, p.from, p.to)
    }

    if (absorber_timers[p.id] === void 0)
      delete locks[p.id]
  }
  const timer = absorber_timers[p.id]
  if (timer != null) clearTimeout(timer)
  absorber_timers[p.id] = setTimeout(() => {
    delete absorber_timers[p.id]
    if (effect_timers[p.id]) {
      flush()
    } else {
      delete locks[p.id]
    }
  }, a('/.#ribbonAbsorbTimeout')) as any

  if (locks[p.id] !== void 0) return

  const prop_adjust = a.get(p.adjust) as $me_atom2
  p.adjust = prop_adjust.name()
  // console.log(p.adjust)

  let prop_from: $me_atom2
  if (typeof p.from != 'number') {
    prop_from = a.get(p.from) as $me_atom2
    p.from = prop_from.name()
  }

  let prop_to: $me_atom2
  if (typeof p.to != 'number') {
    prop_to = a.get(p.to) as $me_atom2
    p.to = prop_to.name()
  }

  if (p.init) p.init()
  const val = $me_ribbon_val({
    from: !prop_from ? p.from as number : prop_from.value(),
    to: !prop_to ? p.to as number : prop_to.value(),
    delta: p.delta,
    fromBack: p.fromBack,
  })
if (!Number.isInteger(val)) console.error({val})
  prop_adjust.value(val)
  if (p.fini) p.fini()

  if (effect_timers[p.id] == null)
    effect_timers[p.id] = setTimeout(flush, a('/.#ribbonEffectTimeout')) as any
}

const effect_timers: {
  [id: string]: number
} = {}

const absorber_timers: {
  [id: string]: number
} = {}

const locks: {
  [id: string]: boolean
} = {}

// ==========================================================================

export function $me_ribbon_val(p: {
  from: number,
  to?: number,
  delta: number,
  fromBack: boolean,
}) {
  if (p.to === void 0) p.to = p.from
  const k = 1 + Math.sqrt((p.from - p.delta - p.to) * (p.fromBack ? 1 : -1)) // TODO: introduce '/.#ribbonEffectResistance' (1/2) instead of Math.sqrt
  const result = Math.round(p.from - p.delta / k)
  // const result = p.from - p.delta / k
  // if (!Number.isInteger(result)) console.error(result, p)
  return result
}

// ==========================================================================
}
