namespace $.$$ {
// ============================================================================

// https://loading.io/css/
export const $me_spinner = <$me_atom2_elem_cnf_intf>{
  base: $me_stylesheet,
  prop: {
  // public:
    color: () => 'white',
    size: () => 64,
    period: () => 1.2,
    qt: () => 12,
    k_height: () => 14 /32,
    k_width: () => 5 /32,

  // internal:
    '#width': '.size',
    '#height': '.size',
    '#align': () => $me_align.center,
    idx: $me_atom2_prop(['.qt'],
      ({masters: [qt]}) => [...Array(Math.floor(qt)).keys()]
    ),

    styleSheetName: () => 'spinner',
    styleSheetCommon: $me_atom2_prop(['.styleSheetName'],
      ({masters: [styleSheetName]}) => `
        @keyframes ${styleSheetName} {
          0% {
            opacity: 1;
          }
          100% {
            opacity: 0;
          }
        }
      `
    ),
    styleSheet: $me_atom2_prop(['.className', '.styleSheetName', '.idx', '.color', '.size', '.qt', '.period', '.k_height', '.k_width'],
      ({masters: [className, styleSheetName, idx, color, size, qt, period, k_height, k_width], atom}) => {
        const semiSize = Math.round(size / 2)
        const top = Math.round(semiSize / 10)
        const left = semiSize - top
        const width = Math.round(semiSize * k_width)
        const height = Math.round(semiSize * k_height)
        const step = period / qt
        return (
          `
            .${className} div {
              transform-origin: ${semiSize}px ${semiSize}px;
              animation: ${styleSheetName} ${period.toFixed(1)}s linear infinite;
            }
            .${className} div:after {
              content: " ";
              display: block;
              position: absolute;
              top: ${top}px;
              left: ${left}px;
              width: ${width}px;
              height: ${height}px;
              border-radius: 20%;
              background: ${color};
            }
          ` + idx.map((idx: string) => `
            .${className} div:nth-child(${+idx + 1}) {
              transform: rotate(${Math.round(+idx * 360 / qt)}deg);
              animation-delay: ${(-period + step * (+idx + 1)).toFixed(2)}s;
            }
          `).join('')
        )
      },
    ),
  },
  elem: {
    div: $me_atom2_prop({keys: ['.idx']},
      () => <$me_atom2_elem_cnf_intf>{},
    ),
  },
}

// ==========================================================================
}
