namespace $.$$ {
// ============================================================================

const width_compute_fn = (p: $me_atom2_compute_fn_p_intf<number, [number, number, number, number]>) => {
  const [maxWidth, minWidth, minMarginHor, viewportWidth] = p.masters
  return Math.max(minWidth, Math.min(maxWidth, viewportWidth - minMarginHor * 2))
}

export const $me_dialog = <$me_atom2_elem_cnf_intf>{
  prop: {
    content: $me_atom2_prop_abstract<$me_atom2_elem_cnf_intf>(),
    target: $me_atom2_prop_abstract<string>(),

    minMarginVer: () => 50,
    minMarginHor: () => 50,
    maxWidth: '/.#viewportWidth',
    maxHeight: '/.#viewportHeight',
    minWidth: () => 100,
    minHeight: () => 100,

    '#width': '/.#viewportWidth',
    '#height': '/.#viewportHeight',
    '#ofsHor': $me_atom2_prop(['<.#clientRect'],
      ({masters: [clientRect]}) => -clientRect.left,
    ),
    '#ofsVer': $me_atom2_prop(['<.#clientRect'],
      ({masters: [clientRect]}) => -clientRect.top,
    ),
    '#zIndex': () => 10,
    '#cursor': () => 'not-allowed',
    // background: $me_atom2_prop_abstract<string>(),
    // 'background': $me_atom2_prop(['/.theme'],
    //   ({masters: [theme]}) => theme == $me_theme.light ? '#F5F8F8' : '#878f9b',
    // ),
  },
  event: {
    mousemove: () => {
      return true
    },
    mousedown: () => {
      return true
    },
    touchstart: () => {
      return true
    },
  },
  style: {
    background: () => 'rgba(0,0,0,.1)'
  },
  elem: {
    wrapper: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#width': $me_atom2_prop(['<.maxWidth', '<.minWidth', '<.minMarginHor', '<.#width'],
          width_compute_fn,
        ),
        '#height': $me_atom2_prop(['<.maxHeight', '<.minHeight', '<.minMarginVer', '<.#height'],
          width_compute_fn,
        ),
        '#ofsHor': $me_atom2_prop(['<.#width', '.#width'],
          ({masters: [width_upper, width]}) => (width_upper - width) / 2,
        ),
        '#ofsVer': $me_atom2_prop(['<.#height', '.#height'],
          ({masters: [height_upper, height]}) => (height_upper - height) / 2,
        ),
        '#cursor': () => null as string,
        '#zIndex': $me_atom2_prop(['<.#zIndex'],
          ({masters: [zIndex]}) => zIndex + 1,
        ),
      },
      // style: {
      //   // background: () => 'white',
      //   background: '<.background',
      //   boxShadow: () => '0 4px 8px 0 rgba(0, 0, 0, 0.35)',
      // },
      elem: {
        content: '<.content',
      },
    },
  },
}

// ==========================================================================
}
