namespace $.$$ {
// ============================================================================

export const $nl_switch = <$me_atom2_elem_cnf_intf>{
  base: $me_select,
  prop: {
    options: $me_atom2_prop_abstract<$me_options>(),
    value: $me_atom2_prop_abstract<string|Set<string>>(),

    colorBorder: () => 'transparent',
    colorBorderSelected: () => 'transparent',

    colorBackground: () => 'transparent',
    colorBackgroundSelected: $me_atom2_prop(['/.theme'],
      ({masters: [theme]}) =>
        theme == $me_theme.light ?
        '#0070a4' :
        '#0facf4'
    ),
    borderRadius: () => 0,
    borderWidth: () => 0,
    paddingHor: () => 0,

    option_ids: $me_atom2_prop_keys(['.options']),
    option_width_min: () => 40,
    colorText: $me_atom2_prop(['/.theme'],
      ({masters: [theme]}) =>
        theme == $me_theme.light ?
        '#0070a4' :
        'white'
    ),
    colorTextSelected: () => 'white',
    fontFamily: '/.fontFamily',
    fontWeight: '/.fontWeight',
    fontWeightSelected: () => 500,//'.fontWeight',
    fontSize: $me_atom2_prop(['.em'],
      $me_atom2_prop_compute_fn_mul(14/16),
    ),
    alignVer: () => $me_align.bottom,
    ofsVer: $me_atom2_prop_abstract(),
    no_adjust: () => true,
  },
}

// ==========================================================================
}
