namespace $.$$ {
// ============================================================================

export const $me_list2_demo_grid = <$me_atom2_elem_cnf_intf>{
  base: $me_list2,
  init() {
    a<Worker>('.worker').addEventListener('message', a<WorkerMessageEventListener>('.workerListener'))
  },
  fini() {
    a<Worker>('.worker').removeEventListener('message', a<WorkerMessageEventListener>('.workerListener'))
  },
  style: {
    border: () => '1px solid red',
  },
  dispatch(dispatch_name: string, dispatch_arg: any) {
    if (dispatch_name == 'item') {
      // console.log(dispatch_arg)
      const {ctx, pixelRatio, ctxLeft,
        ctxRowMarginLeft,
        ctxRowMarginRight,
        ctxFirstRowMarginTop,
        ctxLastRowMarginBottom,
        i, ctxHeight, ctxWidth} = dispatch_arg

      if (i < 0 || i >= a('.count')) {
        dispatch_arg.ctxTop = null
        dispatch_arg.ctxBottom = null
        return true
      }

      const ctxRowHeight = a('.row_height')*pixelRatio

      let {ctxTop, ctxBottom} = dispatch_arg
      if (ctxTop == null) {
        dispatch_arg.ctxTop = ctxTop = ctxBottom - ctxRowHeight
      } else {
        dispatch_arg.ctxBottom = ctxBottom = ctxTop + ctxRowHeight
      }
      const cols = a<string[]>('.cols')
      const colDefs = a<colDefs_type>('.colDefs')
      const fixed_col_width = 80

      const data_cache = a<Map<number, any>>('.data_cache')
      let right = 0
      for (const col_id of cols) {
        const col = colDefs[col_id]
        if (fixed_col_width*pixelRatio + ctxLeft + right*pixelRatio > ctxWidth - ctxRowMarginRight) {
          right += col.width
          continue
        }
        if (fixed_col_width*pixelRatio + ctxLeft + (right + col.width)*pixelRatio < ctxRowMarginLeft) {
          right += col.width
          continue
        }
        if (data_cache.has(i) && col.fld && col.fn) {
          const data = data_cache.get(i)
          // console.log(data)
          const flds = col.fld.map(fld => data[fld])
          const text = col.fn(...flds)
          // console.log(text, flds, col.fld, data)
          const ret = $me_label_text_n_ctxLeft({
            ctx,
            pixelRatio,
            text,
            width: col.width,
            left: fixed_col_width + ctxLeft/pixelRatio + right,
            paddingLeft: 8,
            paddingRight: 8,
            alignHor: col.align,
          })

          const ctxFontSize = a('.fontSize')*pixelRatio
          ctx.fillStyle = a('.colorText')
          ctx.font = a('.fontWeight') + ' ' + ctxFontSize + 'px ' + a('.fontFamily')
          ctx.fillText(
            ret.text,
            ret.ctxLeft,
            ctxTop + (ctxRowHeight + ctxFontSize) / 2,
          )
        }

        right += col.width

        ctx.lineWidth = pixelRatio
        ctx.strokeStyle = 'silver'
        ctx.beginPath()
        ctx.moveTo(fixed_col_width*pixelRatio + ctxLeft + right*pixelRatio, ctxTop)
        ctx.lineTo(fixed_col_width*pixelRatio + ctxLeft + right*pixelRatio, ctxBottom)
        ctx.stroke()
      }
      const ctxRight = dispatch_arg.ctxRight = fixed_col_width*pixelRatio + ctxLeft + right*pixelRatio

      const ctxBorderWidth = pixelRatio/2
      $me_atom2_ctx_rect({
        ctx,
        ctxTop,
        ctxLeft: fixed_col_width*pixelRatio + ctxLeft,
        ctxWidth: ctxRight - (fixed_col_width*pixelRatio + ctxLeft),
        // ctxWidth: ctxWidth - (ctxRight - ctxLeft),
        ctxHeight: ctxRowHeight,
        stroke: {
          ctxWidth: ctxBorderWidth,
          style: 'silver',
        },
      })
      const items_new = a<items_type>('.items_new')
      items_new.set(i, {ctxTop, ctxRowHeight, ctxWidth: right*pixelRatio})

      return true
    } else if (dispatch_name == 'beforeItems') {
      const items_new = a<items_type>('.items_new', new Map())
      // console.log(items_new)

      return true
    } else if (dispatch_name == 'afterItems') {
      const items_new = a<items_type>('.items_new')
      const cols = a<string[]>('.cols')
      const colDefs = a<colDefs_type>('.colDefs')
      if (false) {
        let ctxTopMin: number
        let ctxBottomMax: number
        let ctxWidthMax: number
        for (const [i, item] of items_new) {
          if (ctxTopMin == null || ctxTopMin > item.ctxTop) ctxTopMin = item.ctxTop
          const ctxBottom = item.ctxTop + item.ctxRowHeight
          if (ctxBottomMax == null || ctxBottomMax < ctxBottom) ctxBottomMax = ctxBottom
          if (ctxWidthMax == null || ctxWidthMax < item.ctxWidth) ctxWidthMax = item.ctxWidth
        }
        const pixelRatio = window.devicePixelRatio
        const ctxHeight = ctxBottomMax - ctxTopMin
        const canvas = document.createElement('canvas') as HTMLCanvasElement
        canvas.height = Math.ceil(ctxHeight / pixelRatio)
        canvas.width = Math.ceil(ctxWidthMax/pixelRatio)
        const ctx = canvas.getContext('2d')

        const ctxFontSize = a('.fontSize')*pixelRatio
        for (const [i, item] of items_new) {
          const ctxTop = item.ctxTop = item.ctxTop - ctxTopMin
          const ctxBottom = ctxTop + item.ctxRowHeight
          let right = 0
          for (const col_id of cols) {
            const col = colDefs[col_id]
            ctx.lineWidth = pixelRatio
            ctx.strokeStyle = 'silver'
            const text = col.caption || col_id
            const ret = $me_label_text_n_ctxLeft({
              ctx,
              pixelRatio,
              text,
              width: col.width,
              left: right,
              paddingLeft: 8,
              paddingRight: 8,
            })

            ctx.fillStyle = a('.colorText')
            ctx.font = a('.fontWeight') + ' ' + ctxFontSize + 'px ' + a('.fontFamily')
            ctx.fillText(
              ret.text,
              ret.ctxLeft,
              ctxTop + (item.ctxRowHeight + ctxFontSize) / 2,
            )

            right += col.width

            ctx.lineWidth = pixelRatio
            ctx.strokeStyle = 'silver'
            ctx.beginPath()
            ctx.moveTo(right*pixelRatio, ctxTop)
            ctx.lineTo(right*pixelRatio, ctxBottom)
            ctx.stroke()
          }
        }
        a('.canvas_cache', canvas)
      }
      let i_min: number
      let i_max: number

      // const request_by_idx = {} as any
      const data_cache = a<Map<number, any>>('.data_cache')
      const i_to_be_requested = [] as number[]
      for (const [i, item] of items_new) {
        if (i_min == null || i_min > i) i_min = i
        if (i_max == null || i_max < i) i_max = i
        if (!data_cache.has(i)) {
          i_to_be_requested.push(i)
        }
      }
      a('.i_min', i_min)
      a('.i_max', i_max)
      if (i_to_be_requested.length) {
        const flds = new Set<string>()
        for (const col_id of cols) {
          const col = colDefs[col_id]
          if (col.fld)
            for (const fld of col.fld)
              flds.add(fld)
        }

        {
          const by_idx: any = {}
          for (const i of i_to_be_requested) {
            by_idx[i] = flds
          }
          // console.log(by_idx)
          a<Worker>('.worker').postMessage({cmd: 'recs', tag: a.curr.name(), by_idx})
        }

        {
          const by_idx: any = {}
          const i_min_cache = a('.i_min_cache')
          const i_max_cache = a('.i_max_cache')
          // console.log({i_min_cache, i_max_cache})
          for (let i = i_min_cache; i < i_min; i++)
            if (!data_cache.has(i))
              by_idx[i] = flds
          for (let i = i_max + 1; i <= i_max_cache; i++)
            if (!data_cache.has(i))
              by_idx[i] = flds
          // console.log(by_idx)
          a<Worker>('.worker').postMessage({cmd: 'recs', tag: a.curr.name(), by_idx})
        }
      }

      a('.items_cache', items_new)
      return true
    }
  },
  prop: {
    data_cache_capacity: $me_atom2_prop(['/.#viewportHeight', '.row_height'],
      ({masters: [viewportHeight, row_height]}) => Math.ceil(3 * (viewportHeight / row_height)),
    ),
    data_cache: () => new Map<number, any>(),
    canvas_cache: () => null as HTMLCanvasElement,
    i_min: () => null as number,
    i_max: () => null as number,
    i_min_cache: $me_atom2_prop(['.i_min', '.data_cache_capacity'],
      ({masters: [i_min, data_cache_capacity]}) =>
        Math.max(0, Math.ceil(i_min - data_cache_capacity/3))
    ),
    i_max_cache: $me_atom2_prop(['.i_min_cache', '.data_cache_capacity'],
      ({masters: [i_min_cache, data_cache_capacity]}) =>
        i_min_cache + data_cache_capacity
    ),
    items_cache: () => null as items_type,
    items_new: () => null as items_type,
    '#ofsHor': () => 16,
    '#width': $me_atom2_prop(['/.#viewportWidth', '.#ofsHor'],
      ({masters: [viewportWidth, ofsHor]}) => viewportWidth - 2*ofsHor,
    ),
    params: () => ({
      rmqt: 1,
    }),
    count: $me_atom2_prop(['.params', '.worker'],
      ({atom, masters: [params, worker]}) => {
        const tag = atom.parent().name()
        worker.postMessage({cmd: 'count', tag})
        return null
      },
      ({val}) => {
        a('.isReadyData', val != null)
        console.warn({val})
      },
    ),
    workerListener: $me_atom2_prop<WorkerMessageEventListener>([],
      ({atom}) => {
        const tag = atom.parent().name()
        const atom_count = a.get('.count') as $me_atom2
        const atom_data_cache = a.get('.data_cache') as $me_atom2<Map<number, any>>
        const atom_i_min_cache = a.get('.i_min_cache') as $me_atom2
        const atom_i_max_cache = a.get('.i_max_cache') as $me_atom2
        const atom_i_min = a.get('.i_min') as $me_atom2
        const atom_i_max = a.get('.i_max') as $me_atom2
        const atom_needRender = a.get('.needRender') as $me_atom2<boolean>
        return (event: MessageEvent) => {
          const {cmd} = event.data
          // console.log('%cTHERE', 'background:red;color:white', event)
          if (cmd == 'count' && event.data.tag == tag) {
            const {status, count} = event.data
            if (status == 'ok') {
              atom_count.value(count)
            } else {
              console.error(event.data)
            }
          } else if (cmd == 'recs' && event.data.tag == tag) {
            // console.log(event.data.by_idx)
            const by_idx = event.data.by_idx
            if (by_idx) {
              const data_cache = atom_data_cache.value()
              const i_min_cache = atom_i_min_cache.value()
              const i_max_cache = atom_i_max_cache.value()
              const i_min = atom_i_min.value()
              const i_max = atom_i_max.value()
              for (const idx in event.data.by_idx) {
                const i = +idx
                if (i < i_min_cache || i > i_max_cache) continue
                data_cache.set(i, by_idx[i])
                if (i < i_min || i > i_max) continue
                atom_needRender.value(true)
              }
            }
          }
        }
      },
    ),
    worker: '/.gridWorker',
    isTouch: '/.#isTouch',
    row_height: $me_atom2_prop_either(['.isTouch'],
      () => 44,
      () => 32,
    ),
    fontSize: $me_atom2_prop_either(['.isTouch'],
      () => 16,
      () => 14,
    ),
    firstRowMarginTop: '.row_height',
    colDefs: () => $nl_search_panel_result_cols,
    cols: () => ['', '/', ' ', '', '', '', '', '', '', '', '', '', ' ', '', '', '  ..', '', ' ', ' ', '', ''],
    // ownCanvas: () => document.getElementById('canvas') as HTMLCanvasElement,
    // ownCtx: $me_atom2_prop<CanvasRenderingContext2D, [HTMLCanvasElement]>(['.ownCanvas'],
    //   ({masters: [ownCanvas]}) => ownCanvas.getContext('2d'),
    // ),
  },
}

type items_type = Map<number, item_intf>
interface item_intf {
  ctxTop: number,
  ctxRowHeight: number,
  ctxWidth: number,
}

interface col_intf {
  caption?: string,
  width: number,
  fld?: string[],
  align?: $me_align,
  fn?: any,
}

type colDefs_type = {
  [id: string]: col_intf,
}

type WorkerMessageEventListener = (event: MessageEvent) => void

// ==========================================================================

const root = $me_atom2_entity.root() as $me_atom2_entity
root.props({
  'gridWorker': () => {
    const idx = window.location.pathname.indexOf('/', 1)
    console.log(window.location.origin)
    const root =
      window.location.origin.includes('localhost') ||
      window.location.origin.match(/^https?:\/\/(\d+\.){3}\d+(:\d+)?$/) ?
        '/nl' :
        '/newlook'
    const worker = new Worker(window.location.origin + root + '/data/-/web.js')
    // worker.addEventListener('message', (event: MessageEvent) => {
    //   console.log('%cHERE', 'background:magenta;color:white', event)
    // })
    return worker
  },
})

// ============================================================================
}

