namespace $.$$ {
// ==========================================================================

export const $me_triangle = <$me_atom2_elem_cnf_intf>{
  prop: {
  // public:
    direction: () => $me_rect_sides_enum.bottom,
    size: '.em',
    color: '.colorText',
    k: () => 2 / Math.sqrt(3),

  // internal:
    '#height': $me_atom2_prop(['.direction', '.size', '.k'],
      ({masters: [direction, size, k]}) =>
        direction == $me_rect_sides_enum.bottom || direction == $me_rect_sides_enum.top ?
        size :
        Math.round(size * k)
    ),
    '#width': $me_atom2_prop(['.direction', '.size', '.k'],
      ({masters: [direction, size, k]}) =>
        direction == $me_rect_sides_enum.left || direction == $me_rect_sides_enum.right ?
        size :
        Math.round(size * k)
    ),
  },
  elem: {
    content: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#width': () => 0,
        '#height': () => 0,
      },
      style: {
        borderTop: $me_atom2_prop(['<.direction', '<.#height', '<.color'],
          ({masters: [direction, height, color]}) =>
            direction == $me_rect_sides_enum.top ? '' :
            direction == $me_rect_sides_enum.bottom ? `${height}px solid ${color}` :
            `${height / 2}px solid transparent`,
        ),
        borderLeft: $me_atom2_prop(['<.direction', '<.#width', '<.color'],
          ({masters: [direction, width, color]}) =>
            direction == $me_rect_sides_enum.left ? '' :
            direction == $me_rect_sides_enum.right ? `${width}px solid ${color}` :
            `${width / 2}px solid transparent`,
        ),
        borderRight: $me_atom2_prop(['<.direction', '<.#width', '<.color'],
          ({masters: [direction, width, color]}) =>
            direction == $me_rect_sides_enum.right ? '' :
            direction == $me_rect_sides_enum.left ? `${width}px solid ${color}` :
            `${width / 2}px solid transparent`,
        ),
        borderBottom: $me_atom2_prop(['<.direction', '<.#height', '<.color'],
          ({masters: [direction, height, color]}) =>
            direction == $me_rect_sides_enum.bottom ? '' :
            direction == $me_rect_sides_enum.top ? `${height}px solid ${color}` :
            `${height / 2}px solid transparent`,
        ),
      },
    },
  },
}

// ==========================================================================
}
