namespace $.$$ {
// ============================================================================

const min_max = (prefix: string, min = 'min', max = 'max') =>
  [prefix + '_' + min, prefix + '_' + max]

export const $me_atom2_list_row_height_source_fn_apply : $me_atom2_apply_fn<number> =
  ({key: [row_i], val, prev, atom}) => {
    if (val == null || val < 0 ||
      +row_i >= a('.row_count') ||
      $me_list_row_i_out_of_range_is(+row_i, a('.row_i_min'), a('.row_i_max'))
    ) return

    set_row_height(a(`.rec_idx[${row_i}]`), a('._provider'), a('.provider_tag'), val)
    if (prev == null || prev < 0) return

    const delta = prev - val
    if (!delta) return

    if (delta < 0) {
      // console.error(atom.name(), {delta, prev, val})
      const top = a(`.row_top[${row_i}]`)
      // const delta_bottom = top + val - a('.#height')
      const delta_bottom = top + val - a('.height_actual')
      if (delta_bottom > 0) {
        const delta_top = top - a('.header_height')
        if (delta_top > 0) {
          const delta = Math.min(delta_bottom, delta_top)
          const row_i_min = a('.row_i_min')
          const top = a(`.row_top[${row_i_min}]`)
          a('.visible_top', top - delta)
          a(`.row_top[${row_i_min}]`, $me_atom2_anim({to: top - delta}))
        }
      }
    } else {
      const val = a('.visible_top')
      const idx_max = a('.rec_idx_max')
      const i = a('.row_i_min')
      const idx = a('.visible_idx_min')
      const len = a('.row_count')
      const _provider = a<$me_atom2_path>('._provider')
      const provider_tag = a<string>('.provider_tag')
      // const height = a('.#height') + delta
      const height = a('.height_actual') + delta
      const header_height = a('.header_height')
      const p = {val, idx_max, i, idx, len, _provider, provider_tag, height, header_height}

      const rec_idx = a(`.rec_idx[${row_i}]`)
      const row_height: row_height_fn =
        (idx, _provider, provider_tag) =>
          idx == rec_idx ? prev : get_row_height(idx, _provider, provider_tag)

      compute_visible_helper(p, true, row_height)

      set_visible(p, true)

      if (p.idx == idx_max) {
        const ofs = height - p.val
        if (ofs > 0) {
          p.idx = idx
          p.val = val + row_height(p.idx, p._provider, p.provider_tag)
          p.i = i
          p.header_height = header_height - ofs
          compute_visible_helper(p, false, row_height)
          set_visible(p, false)
          a(`.row_top[${p.i}]`, $me_atom2_anim({to: a(`.row_top[${p.i}]`) + ofs}))
        }
      }
    }
  }

export const $me_list = <$me_atom2_elem_cnf_intf>{
  dispatch: (dispatch_name: string, dispatch_arg: any) => {
    if (dispatch_name == 'row_height_default') {
      dispatch_arg.val = a('.row_height_min')
      return true
    } else if (dispatch_name == 'get_row_height') {
      const row_heights = a<Map<number, number>>('.row_heights')
      dispatch_arg.val =
        row_heights.has(dispatch_arg.idx) ?
        row_heights.get(dispatch_arg.idx) :
        a.dispatch('', 'row_height_default', {idx: dispatch_arg.idx, val: 0}).val
      return true
    } else if (dispatch_name == 'set_row_height') {
      const row_heights = a<Map<number, number>>('.row_heights')
      if (dispatch_arg.val != a.dispatch('', 'row_height_default', {idx: dispatch_arg.idx, val: 0}).val) {
        row_heights.set(dispatch_arg.idx, dispatch_arg.val)
      } else if (row_heights.has(dispatch_arg.idx)) {
        row_heights.delete(dispatch_arg.idx)
      }
      return true
    } else if (dispatch_name == 'set_view') {
      const val = dispatch_arg || {}
      const elem = a.curr as $me_atom2_ec
      {
        const prop_row_height_source = elem._entities.prop['row_height_source']
        const key = prop_row_height_source._entities.key
        if (key)
          for (const k in key) {
            const atom = key[k] as $me_atom2
            atom.value(null)
          }
      }
      {
        const prop_row_height_source = elem._entities.prop['row_height']
        const key = prop_row_height_source._entities.key
        if (key)
          for (const k in key) {
            const atom = key[k] as $me_atom2
            atom.value(null)
          }
      }
      a('.provider_tag', val.id)
      a('.row_i_min', val.row_i_min || 0)
      a('.row_i_max', (a('.row_i_min') + a('.row_count') - 1) % a('.row_count'))
      a('.visible_idx_min', val.visible_idx_min || 0)
      a('.visible_top', val.visible_top || a('.header_height'))
      // console.log({visible_top: val.visible_top, visible_idx_min: val.visible_idx_min, row_i_min: val.row_i_min})
      return true
    } else if (dispatch_name == 'iterate_rows') {
      const row_count = a('.row_count')
      if (row_count) {
        const row_i_max = a('.row_i_max')
        if (row_i_max >= 0) {
          let row_i = a('.row_i_min')
          while (dispatch_arg(row_i) !== false && row_i != row_i_max)
            row_i = next_i(1, row_i, row_count)
        }
      }
      return true
    }
    return false
  },
  prop: {

    // in above elem:
    // header_height: () => 32,
    // row_height_min: () => 28,
    // _provider: () => a.curr.parent(true).path,
    // provider_tag: () => null as string,
    header_height: $me_atom2_prop_abstract<number>(),
    row_height_min: $me_atom2_prop_abstract<number>(),
    rec_count: $me_atom2_prop_abstract<number>(),
    provider_tag: $me_atom2_prop_abstract<number>(),
    header_content: $me_atom2_prop_abstract<number>(),
    row_content: $me_atom2_prop_abstract<number>(),

    hidden_curtain: () => false,
    height_curtain: () => 40,
    width_curtain: '.#width',

    curtain_kind: () => 'black' as 'black' | 'white',

    curtain: () => ['top', 'bottom'],
    curtainVisible: $me_atom2_prop(
      {
        keys: ['.curtain'],
        masters: $me_atom2_prop_masters([],
          ({key: [curtain]}) => {
            if (curtain == 'top') {
              return ['.visible_idx_min', '.visible_top', '.header_height']
            } else {
              return ['.visible_idx_max', '.rec_idx_max', '.visible_bottom', '.#height']
            }
          }
        ),
      },
      ({key: [curtain], masters}) => {
        if (curtain == 'top') {
          const [visible_idx_min, visible_top, header_height] = masters
          return visible_idx_min || visible_top < header_height
        } else {
          const [visible_idx_max, rec_idx_max, visible_bottom, height] = masters
          return visible_idx_max < rec_idx_max || visible_bottom > height
        }
      },
    ),

    _provider: () => a.curr.parent(true).path,
    // provider_tag: () => null as string,

    rec_idx_delta: () => 1,
    rec_idx_max: $me_atom2_prop(['._rec_count'],
      ({masters: [rec_count]}) => rec_count - 1,
    ),
    row_count: $me_atom2_prop(['.#height', '.row_height_min', '.header_height', '.rec_count'],
      ({masters: [height, row_height_min, header_height, rec_count]}) =>
        rec_count < 0 ? 0 :
        2 + Math.min(rec_count,
          Math.ceil(
            Math.max(0, height - header_height) / row_height_min
          )
        ),
      ({prev, val}) => {
        const result = prev != null && prev > val ? prev : val
        return result
      },
    ),
    _rec_count: $me_atom2_prop(['.rec_count'],
      null,
      ({val, prev, atom}) => {
        if (val == null || prev == null) return
        for (let i = 0; i < a('.row_count'); i++) {
          a(`.row_height_source[${i}]`, null)
          a(`.row_height[${i}]`, null)
        }
      }
    ),
    row_i: $me_atom2_prop<string[]>(['.row_count'],
      ({masters: [row_count]}) => [...Array(row_count).keys()].map(i => i + ''), // https://stackoverflow.com/questions/3746725/how-to-create-an-array-containing-1-n/33352604#33352604
    ),
    row_i_min: () => 0,
    row_i_max: () => -1,
    ...$me_atom2_prop_same_def(
      () => 0,
      [
      ...min_max('visible_idx'),
      ...min_max('visible', 'top', 'bottom')
    ]),
    row_hidden: $me_atom2_prop<boolean>(
      {keys: ['.row_i'], masters: ['.row_i_min', '.row_i_max']},
      ({key: [row_i], masters: [row_i_min, row_i_max]}) => $me_list_row_i_out_of_range_is(+row_i, row_i_min, row_i_max),
    ),
    rec_idx: $me_atom2_prop<number, number[]>(
      {
        keys: ['.row_i'],
        masters: $me_atom2_prop_masters(['.row_i_min', '.row_i_max', '.row_count'],
          ({key: [row_i], masters: [row_i_min, row_i_max, row_count]}) => {
            const key = +row_i
            const result = (() => {
              if ($me_list_row_i_out_of_range_is(key, row_i_min, row_i_max)) return []
              if (key == row_i_min) return ['.visible_idx_min']
              if (!row_count) return []
              const i = next_i(-1, key, row_count)
              return [`.rec_idx[${i}]`, '.rec_idx_delta']
            })()
            return result
          },
        ),
      },
      ({len, masters}) => !len ? null : masters.reduce((sum, val) => sum + val, 0),
      ({key: [row_i], val}) => {
        a(`.row_height_source[${row_i}]`, null)
        a(`.row_height[${row_i}]`, null)
      },
    ),
    row_top: $me_atom2_prop<number, number[]>(
      {
        keys: ['.row_i'],
        masters: $me_atom2_prop_masters(['.row_i_min', '.row_i_max', '.row_count'],
          ({key: [row_i], masters: [row_i_min, row_i_max, row_count]}) => {
            const key = +row_i
            const result = (() => {
              if ($me_list_row_i_out_of_range_is(key, row_i_min, row_i_max)) return []
              if (key == row_i_min) return ['.visible_top']
              if (!row_count) return []
              const i = next_i(-1, key, row_count)
              return [`.row_top[${i}]`, `.row_height[${i}]`]
            })()
            return result
          },
        ),
      },
      ({len, masters}) => !len ? null : masters.reduce((sum, val) => sum + val, 0),
    ),
    row_heights_store: () => new Map<string, Map<number, number>>(),
    row_heights: $me_atom2_prop(['.provider_tag', '.row_heights_store'],
      ({masters: [tag, holder]}) => holder[tag] || (holder[tag] = new Map<number, number>())
    ),
    row_height_source: $me_atom2_prop(
      {
        keys: ['.row_i'],
        masters: $me_atom2_prop_masters(['._rec_count', '.row_i_min', '.row_i_max'],
          ({key: [row_i], masters: [rec_count, row_i_min, row_i_max]}) =>
            rec_count <= 0 || $me_list_row_i_out_of_range_is(+row_i, row_i_min, row_i_max) ? [] :
            [`.rec_idx[${row_i}]`, '._provider', '.provider_tag'],
        ),
      },
      ({len, masters: [rec_idx , _provider, provider_tag]}) => {
        const result = !len || !_provider || !provider_tag ? -1 : get_row_height(rec_idx, _provider, provider_tag, true)
        return result
      },
      $me_atom2_list_row_height_source_fn_apply,
    ),
    row_height: $me_atom2_prop(
      {keys: ['.row_i'], masters: ['.row_height_source[]', '.row_i_min', '.row_i_max']},
      ({key: [row_i], masters: [to, row_i_min, row_i_max], prev}) => {
        const result =
          $me_list_row_i_out_of_range_is(+row_i, row_i_min, row_i_max) ? null :
          to < 0 ? null :
          prev == null || prev == to ? to :
          $me_atom2_anim({
            to,
            fini: () => adjust_rows(a('.visible_top')),
          })
        return result
      },
      ({val}) => val == null ? null : Math.round(val),
    ),
    adjust_rows: $me_atom2_prop(['.height_actual', '._rec_count'],
      null,
      ({val, atom}) => {
        adjust_rows(a('.visible_top'))
      },
    ),
    adjust_top: $me_atom2_prop([],
      () => null,
      ({val}) => {
        if (val == null) return
        adjust_rows(val, true)
      }
    ),
    adjust_bottom: $me_atom2_prop([],
      () => null,
      ({val}) => {
        if (val == null) return
        adjust_rows(val)
      }
    ),
    adjust_height_actual: $me_atom2_prop(
      $me_atom2_prop_masters(['._rec_count', '.row_i_max'],
        ({masters: [rec_count, row_i_max]}) => {
          if (!rec_count || row_i_max < 0) return ['.#height']
          const result = ['.#height']
          if (row_i_max == rec_count - 1) {
            result.push('.header_height')
            for (let i = 0; i <= row_i_max; i++)
              result.push(`.row_height[${i}]`)
          }
          return result
        }
      ),
      ({len, masters, prev}) => {
        if (!len) return 0
        const [height] = masters
        if (len == 1) return height
        let result = 0
        for (let i = 1; i < len; i++) {
          if (masters[i] == null) return prev
          result += masters[i]
          if (result > height) return height
        }
        return result
      },
      ({val, atom}) => {
        a('.height_actual', val)
      }
    ),
    height_actual: '.#height',
    // disabledScroll: () => false,
    disabledScroll: $me_atom2_prop(['.visible_idx_min', '.visible_idx_max', '.visible_top', '.visible_bottom', '.rec_count', '.header_height', '.#height'],
      ({masters: [visible_idx_min, visible_idx_max, visible_top, visible_bottom, rec_count, header_height, height]}) =>
        !visible_idx_min && visible_idx_max == rec_count - 1 && visible_top >= header_height && visible_bottom <= height
    ),
    '#order': () => ['row', 'header'],
  },
  elem: {
    header: $me_atom2_prop(['.header_content'],
      ({masters: [header_content]}) => !header_content ? null : <$me_atom2_elem_cnf_intf>{
        prop: {
          '#height': '<.header_height',
        },
        elem: {
          content: () => header_content
        },
      },
    ),
    row: $me_atom2_prop<$me_atom2_elem_cnf_intf>({keys: ['.row_i']},
      ({key: [row_i]}) => <$me_atom2_elem_cnf_intf>{
        prop: {
          '#ofsVer': `<.row_top[${row_i}]`,
          '#height': `<.row_height[${row_i}]`,
          '#hidden': `<.row_hidden[${row_i}]`,
        },
        elem: {
          content: `<.row_content[${row_i}]`,
        },
      },
    ),
    curtain: $me_atom2_prop<$me_atom2_elem_cnf_intf>({keys: ['.curtain'], masters: ['.hidden_curtain', '.curtainVisible[]']},
      ({key: [curtain], masters: [hidden_curtain, curtainVisible]}) =>
        hidden_curtain || !curtainVisible ? null : <$me_atom2_elem_cnf_intf>{
          prop: {
            '#height': '<.height_curtain',
            // '#width': '<.#width',
            '#width': '<.width_curtain',
            '#alignVer': () => $me_align[curtain],
          },
          style: {
            background: $me_atom2_prop(['<.curtain_kind'],
              ({masters: [kind]}) =>
                kind == 'black' ?
                `linear-gradient(${curtain == 'bottom'? 0 : 180}deg, rgba(0,0,0,.24) 0%, rgba(0,0,0,.1) 33%, rgba(0,0,0,0) 100%)` :
                `linear-gradient(${curtain == 'bottom'? 0 : 180}deg, rgba(255,255,255,.9) 0%, rgba(255,255,255,.5) 50%, rgba(255,255,255,0) 100%)`,
            ),
            pointerEvents: () => 'none',
          },
        },
    ),
  },
  style: {
    overflow: () => 'hidden',
  },
  event: {
    wheel: p =>
      !a('.disabledScroll') &&
      p.isInRect(p.event.clientX, p.event.clientY) &&
      $me_atom2_event_wheel_y_is(p.event) &&
      wheel(p.event.deltaY),
    wheelDrag: p =>
      !a('.disabledScroll') &&
      p.isInRect(p.event.start.clientX, p.event.start.clientY) &&
      $me_atom2_event_wheel_y_is(p.event) &&
      wheel(p.event.deltaY),
    wheelTouch: p =>
      !a('.disabledScroll') &&
      p.isInRect(p.event.start.touches[0].clientX, p.event.start.touches[0].clientY) &&
      $me_atom2_event_wheel_y_is(p.event) &&
      wheel(p.event.deltaY),
  },
}

function wheel(deltaY: number) {
  const fromBottom = deltaY < 0
  if (
    fromBottom ?
    a('.visible_idx_min') > 0 || a('.visible_top') < a('.header_height') :
    a('.visible_idx_max') < a('.rec_idx_max') || a('.visible_bottom') > a('.height_actual')
  ) {
    a(
      fromBottom ? '.adjust_top' : '.adjust_bottom',
      a(fromBottom ? '.visible_bottom' : '.visible_top') - deltaY
    )
  } else {
    $me_ribbon_effect({
      id: a.curr.name(),
      init: () => { skip_limit = true },
      fini: () => { skip_limit = false },
      adjust: fromBottom ? '.adjust_bottom' : '.adjust_top',
      from: fromBottom ? '.visible_top' : '.visible_bottom',
      to: fromBottom ? '.header_height' : '.height_actual',
      delta: deltaY,
      fromBack: fromBottom,
    })
  }
  return true
}


let skip_limit = false

export const $me_list_row_i_out_of_range_is = (key: number, row_i_min: number, row_i_max: number) =>
  row_i_max < 0 ||
  row_i_max >= row_i_min && (key < row_i_min || key > row_i_max) ||
  row_i_max < row_i_min && key > row_i_max && key < row_i_min

const next_i = (inc: number, i: number, len: number) => {
  if (!len) $me_throw('len == 0')
  return (i + len + inc % len) % len
}

const get_row_height = (idx: number, _provider: $me_atom2_path, provider_tag: string, non_cache = false) => {
  let result: number
  if (!non_cache) {
    const visible_idx_min = a('.visible_idx_min')
    const visible_idx_max = a('.visible_idx_max')
    if (visible_idx_min != null && visible_idx_max != null && visible_idx_min <= idx && idx <= visible_idx_max) {
      const i = next_i(idx - visible_idx_min, a('.row_i_min'), a('.row_count'))
      result = a(`.row_height[${i}]`)
    }
  }
  if (result == null) {
    const dispatch_arg = {idx, tag: provider_tag} as any
    a.dispatch(_provider, 'get_row_height', dispatch_arg)
    result = dispatch_arg.val
  }
  return Math.round(result)
}

const set_row_height = (idx: number, _provider: $me_atom2_path, provider_tag: string, val: number) => {
  a.dispatch(_provider, 'set_row_height', {idx, val, tag: provider_tag})
}

type row_height_fn = (idx: number, _provider: $me_atom2_path, provider_tag: string, non_cache?: boolean) => number

function adjust_rows(val: number, fromBottom = false, row_height?: row_height_fn) {
  if (!row_height) row_height = get_row_height
  if (a('._rec_count') <= 0) {
    a('.row_i_max', -1)
  } else {
    const p = {
      val,
      idx_max: a('.rec_idx_max'),
      i: a(fromBottom ? '.row_i_max' : '.row_i_min'),
      idx: a(fromBottom ? '.visible_idx_max' : '.visible_idx_min'),
      len: a('.row_count'),
      _provider: a<$me_atom2_path>('._provider'),
      provider_tag: a<string>('.provider_tag'),
      height: a('.height_actual'),
      header_height: a('.header_height'),
    }
    compute_visible(p, !fromBottom, row_height)
    set_visible(p, !fromBottom)
    compute_visible(p, fromBottom, row_height)
    set_visible(p, fromBottom)
  }
}

function compute_visible(p: compute_visible_p_intf, bottom = false, row_height?: row_height_fn) {
  if (!row_height) row_height = get_row_height
  compute_visible_helper(p, bottom, row_height)
  if (!skip_limit) {
    if (
      !bottom ?
      p.val > p.header_height :
      (p.val < p.height) && p.i != a('.rec_count') - 1
    ) {
      const from = bottom ? p.height : p.header_height
      p.val = $me_ribbon_val({
        from,
        delta: from - p.val,
        fromBack: !bottom,
      })
    }
  }
  return p
}

function compute_visible_helper(p: compute_visible_p_intf, bottom: boolean, row_height: row_height_fn) {
  let did_step = false
  if (bottom) {
    for (; ((p.val += row_height(p.idx , p._provider, p.provider_tag )) < p.height || !did_step) && p.idx < p.idx_max; p.idx++) {
      p.i = next_i(+1, p.i, p.len)
      did_step = true
    }
  } else {
    for (; ((p.val -= row_height(p.idx , p._provider, p.provider_tag )) > p.header_height || !did_step) && p.idx; p.idx--) {
      p.i = next_i(-1, p.i, p.len)
      did_step = true
    }
  }
  return p
}

interface compute_visible_p_intf extends set_visible_p_intf {
  height?: number,
  header_height?: number,
  len: number,
  idx_max?: number,
  _provider: $me_atom2_path,
  provider_tag: string,
}

function set_visible(p: set_visible_p_intf, bottom = false) {
  a(bottom ? '.row_i_max' : '.row_i_min', p.i)
  a(bottom ? '.visible_idx_max' : '.visible_idx_min', p.idx)
  a(bottom ? '.visible_bottom' : '.visible_top', p.val)
}

interface set_visible_p_intf {
  i: number,
  val: number,
  idx: number,
}

// ==========================================================================
}
