namespace $.$$ {
// ============================================================================

export const $me_iosmenu = <$me_atom2_elem_cnf_intf>{
  prop: {
    target: $me_atom2_prop_abstract<string>(),
    // items: () => ['', '...'],
    limitRight: '/.#viewportWidth',
    limitLeft: () => 0,

    triangleSize: () => 15,
    paddingHor: () => 6,
    borderRadius: () => 8,
    colorBackground: () => 'black',

    '#width': () => 0,
    lambda: () => .5,
    '#height': () => 34,
    '#ofsVer': $me_atom2_prop(['.triangleSize', '.#height'],
      ({masters: [triangleSize, height]}) => - (triangleSize + height)
    ),
    // target: () => null as $me_atom2_ec,
  },
  elem: {
    triangle: () => <$me_atom2_elem_cnf_intf>{
      base: $me_triangle,
      prop: {
        k: () => 2,
        size: '<.triangleSize',
        color: '<.colorBackground',
        '#ofsVer': '<.#height',
        '#ofsHor': $me_atom2_prop(['<.#width', '.#width', '<.lambda', '<.paddingHor'],
          ({masters: [width_parent, width_own, lambda, paddingHor]}) =>
          paddingHor + Math.round((width_parent - 2 * paddingHor - width_own) * lambda)
        ),
      },
    },
    items: () => <$me_atom2_elem_cnf_intf>{
      control: {
        item: $me_atom2_prop<$me_atom2_control_cnf_intf>(
          {keys: ['<.items']},
          ({key: [item]}) => <$me_atom2_control_cnf_intf>{
            base: $me_label,
            prop_non_render: {
              '#cursor': () => 'pointer',
            },
            event: {
              clickOrTap: () => {
                // console.log(item)
                // const target = a.get('<<<') as $me_atom2_ec
                // if (!target) return false
                // console.log(a('<<.target'))
                a.dispatch('<<' + a('<<.target'))//, '', item)
                return true
              },
            },
            prop: {
              '#height': '<.#height',
              alignVer: () => $me_align.center,
              '#ofsHor': $me_atom2_prop(
                $me_atom2_prop_masters(['<<.items'],
                  ({masters: [items]}) => {
                    const idx = items.indexOf(item)
                    const result =
                      !idx ? ['<<.paddingHor'] :
                      [`<^item[${items[idx - 1]}].#ofsHor`, `<^item[${items[idx - 1]}].#width`
                      ]
                    return result
                  }
                ),
                ({len, masters: [left, width, space]}) => len == 1 ? left : left + width,
              ),
              borderWidthLeft: $me_atom2_prop(['<<.items'],
                ({masters: [items]}) => {
                  const idx = items.indexOf(item)
                  return !idx ? 0 : 1
                }
              ),
              colorBorderLeft: () => 'rgba(255,255,255,0.5)',
              paddingHor: () => 10,
              text: () => item,
              fontSize: () => 14,
              fontWeight: () => 400,
              fontFamily: () => '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol"', // https://medium.com/needmore-notes/using-system-fonts-for-web-apps-bf76d214a0e0
              colorText: () => 'white',
            },
          },
        ),
      },
    },
  },
  init: () => {
    const items = a<string[]>('.items')
    const item_last = items[items.length - 1]
    const width = a(`@items^item[${item_last}].#ofsHor`) + a(`@items^item[${item_last}].#width`) + a('.paddingHor')
    const ofsHor = Math.round((a('<.#width') - width) / 2)
    const clientRect = a<$me_rect_intf>('<.#clientRect')
    const limitRight = a('.limitRight') - clientRect.left - width
    const limitLeft = a('.limitLeft') - clientRect.left
    const ofsHor_valid = Math.max(limitLeft, Math.min(ofsHor, limitRight))
    const lambda = .5 + Math.min(.5,
      (ofsHor - ofsHor_valid) / (width - 2 * a('.paddingHor') - a('@triangle.#width'))
    )
    a('.#ofsHor', ofsHor_valid)
    a('.#width', width)
    a('.lambda', lambda)
  },
  style: {
    background: '.colorBackground',
    borderRadius: '.borderRadius',
  },
}

// ==========================================================================
}
