namespace $.$$ {
// ============================================================================

export const $nl_button = <$me_atom2_elem_cnf_intf>{
  type: '$nl_button',
  base: $me_stylesheet,
  prop: {
    caption: $me_atom2_prop_abstract<string>(),
    target: $me_atom2_prop_abstract<string>(),
    source: () => null as string,
    cmd: () => null as string,

    '#width': () => 200,
    '#height': () => 40,
    '#cursor': () => 'pointer',

    '#zIndex': $me_atom2_prop(['<.#zIndex'],
      ({masters: [zIndex]}) => zIndex + 1,
    ),

    styleSheetName: () => 'nl_button',
    styleSheetCommon: $me_atom2_prop(['.styleSheetName'],
      ({masters: [className]}) => `
        .${className} {
          user-select: none;
        }
        .${className} > div {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      `
    ),
    colorBackground: '/.colorButton',
    colorText: () => 'white',
  },
  elem: {
    caption: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#width': () => null as number,
        '#height': () => null as number,
        '#align': () => $me_align.center,
        fontSize: '<.fontSize',
        colorText: '<.colorText',
      },
      dom: {
        innerText: '<.caption',
      },
    },
  },
  style: {
    background: '.colorBackground',
    borderRadius: $me_atom2_prop(['.#height'],
      $me_atom2_prop_compute_fn_mul(1/2),
    ),
  },
  event: {
    clickOrTap: () => {
      a.dispatch(a('.target'), a('.source'), a('.cmd'))
      return true
    },
  },
}

// ==========================================================================
}
