namespace $.$$ {
// ============================================================================

export interface $me_label_text_n_ctxLeft_intf {
  text: string,
  ctxLeft: number,
}

const period_default = '...'
export function $me_label_text_n_ctxLeft(p: {
  ctx: CanvasRenderingContext2D,
  pixelRatio: number,
  text: string,
  width: number,

  period?: string,
  left?: number,
  paddingLeft?: number,
  paddingRight?: number,
  alignHor?: $me_align,
}) {
  let {ctx, text, period, pixelRatio, width, left, paddingLeft, paddingRight, alignHor} = p
  if (period == null) period = period_default
  if (left == null) left = 0
  if (paddingLeft == null) paddingLeft = 0
  if (paddingRight == null) paddingRight = 0
  if (alignHor == null) alignHor = $me_align.left

  const ctxContentWidth = Math.max(0, width - paddingLeft - paddingRight) * pixelRatio
  $me_atom2_control.font_prepare(ctx, pixelRatio)
  let ctxTextWidth = ctx.measureText(text).width
  let ctxLeft = pixelRatio * (left + paddingLeft)
  if (ctxTextWidth > ctxContentWidth) {
    const ctxPeriodWidth = ctx.measureText(period).width
    if (ctxContentWidth < ctxPeriodWidth) {
      console.error(a.curr.name(), {ctxContentWidth, ctxPeriodWidth})
      return {text, ctxLeft}
    }
    let len = text.length, wi = ctxTextWidth, s: string
    while (len && wi > ctxContentWidth - ctxPeriodWidth)
      wi = ctx.measureText(s = text.slice(0, --len)).width
    ctxTextWidth = wi + ctxPeriodWidth
    text = s + period
  }
  ctxLeft += $me_align_correction(
    // a<$me_align>('.alignHor'),
    alignHor,
    () => Math.max(0, ctxContentWidth - ctxTextWidth)
  )
  return {text, ctxLeft}
}

export const $me_label = <$me_atom2_control_cnf_intf>{
  type: '$me_label',
  base: $me_panel,
  prop: {

  // required
    text: $me_atom2_prop_abstract<string>(),

  // optional
    period: () => period_default,

    ...$me_atom2_prop_cascade<$me_align>(() => $me_align.left,
      'align', ['alignHor', 'alignVer']
    ),

    ...$me_atom2_prop_cascade<$me_align>(() => $me_align.left,
      'ofs', ['ofsHor', 'ofsVer']
    ),

  // internal
    _text_n_ctxLeft: $me_atom2_prop<$me_label_text_n_ctxLeft_intf, [CanvasRenderingContext2D, string, string, number, number, number, number, number, $me_align]>([
        '.#ctx', '.text', '.period', '/.#pixelRatio', '.#width', '.#left', '.paddingLeft', '.paddingRight', '.alignHor'
      ],
      ({masters: [ctx, text, period, pixelRatio, width, left, paddingLeft, paddingRight, alignHor]}) =>
        $me_label_text_n_ctxLeft({ctx, text, period, pixelRatio, width, left, paddingLeft, paddingRight, alignHor}) ,
    ),

    _ctxLeft: $me_atom2_prop<number, [$me_label_text_n_ctxLeft_intf]>(['._text_n_ctxLeft'],
      ({masters: [val]}) => val.ctxLeft,
    ),

    _text: $me_atom2_prop<string, [$me_label_text_n_ctxLeft_intf]>(['._text_n_ctxLeft'],
      ({masters: [val]}) => val.text,
    ),

    _textWidth: $me_atom2_prop<number, [CanvasRenderingContext2D, string, number]>(
      ['.#ctx', '.text', '/.#pixelRatio', '.fontSize', '.fontWeight', '.fontFamily'],
      ({masters: [ctx, text, pixelRatio]}) => {
        $me_atom2_control.font_prepare(ctx, pixelRatio)
        const result = Math.ceil(ctx.measureText(text).width / pixelRatio)
        return result
      },
    ),

    ...$me_atom2_prop_same_fn_compute($me_atom2_prop_compute_fn_sum(), {
      '#width': ['._textWidth', '.paddingLeft', '.paddingRight'],
      '#height': ['.fontSize', '.paddingTop', '.paddingBottom'],
    }),
  },
  render: p => {
    let {ctxWidth, ctxHeight} = p
    const ctxFontSize = $me_atom2_control.font_prepare(p.ctx, p.pixelRatio)
    const paddingLeft = a('.paddingLeft')
    const paddingRight = a('.paddingRight')
    const ctxPaddingLeft = Math.round(p.pixelRatio * paddingLeft)
    const ctxPaddingRight = Math.round(p.pixelRatio * paddingRight)
    const ctxPaddingTop = Math.round(p.pixelRatio * a('.paddingTop'))
    const ctxPaddingBottom = Math.round(p.pixelRatio * a('.paddingBottom'))

    ctxHeight -= ctxPaddingTop + ctxPaddingBottom
    if (ctxHeight < ctxFontSize - 1) { // -1 -- for rounding result
      console.error({ctxHeight, ctxFontSize})
      return
    }

    const align = a<$me_align>('.alignVer')
    const ctxOfs = a('.ofsVer') * p.pixelRatio
    const ctxCorrection =
      align == $me_align.bottom ? ctxOfs :
      align == $me_align.top ? ctxHeight - ctxFontSize - ctxOfs :
      (ctxHeight - ctxFontSize) / 2 - ctxOfs
    const bottom = p.ctxRect.bottom - ctxPaddingBottom - ctxCorrection
    p.ctx.fillStyle = a<string>('.colorText')
    let text = a<string>('._text') + ''
    const ctxLeft = a('._ctxLeft')
    ctxWidth -= ctxLeft - a('.#left') * p.pixelRatio
    while (p.ctx.measureText(text).width > ctxWidth)
      text = text.slice(0, -1)
    p.ctx.fillText(text, ctxLeft, bottom)
  },
}

// ============================================================================
}
