namespace $.$$ {
// ============================================================================

export const $nl_picker = <$me_atom2_elem_cnf_intf>{
  base: $me_dropdown,
  dispatch: (dispatch_name: string, dispatch_arg: any) => {
    if (dispatch_name == 'dropdown') {
      const {prop_isDropdown, prop_clientRect, isTouch} = dispatch_arg

      const prop_itemMarginTopFirst = a.get('.itemMarginTopFirst') as $me_atom2
      const prop_itemMarginBottomLast = a.get('.itemMarginBottomLast') as $me_atom2
      const prop_itemMarginVer = a.get('.itemMarginVer') as $me_atom2
      const prop_options = a.get('.options') as $me_atom2
      const prop_option_ids  = a.get('.option_ids') as $me_atom2
      const prop_value = a.get('.value') as $me_atom2<Set<string>|string|Map<string,null>>
      const prop_itemFontSize = a.get('.itemFontSize') as $me_atom2<string>
      const prop_row_height_min = a.get('.row_height_min') as $me_atom2
      const prop_dropdownWidth = a.get('.dropdownWidth') as $me_atom2
      const prop_dropdownHeight = a.get('.dropdownHeight') as $me_atom2
      const prop_theme = a.get('/.theme') as $me_atom2

      dispatch_arg.result = <$me_atom2_elem_cnf_intf>{
        base: $me_list,
        dispatch: (dispatch_name: string, dispatch_arg: any) => {
          if (dispatch_name == 'row_height_default') {
            const idx = dispatch_arg.idx
            dispatch_arg.val = a('.row_height_min') + (
              0 < idx && idx < a('.rec_count') - 1 ? 0 :
              (!idx ? prop_itemMarginTopFirst.value() : prop_itemMarginBottomLast.value()) - prop_itemMarginVer.value()
            )
            return true
          }
          return false
        },
        prop: {
          '#width': prop_dropdownWidth.name(),
          '#height': prop_dropdownHeight.name(),
          rec_count: $me_atom2_prop([prop_option_ids.name()],
            ({masters: [ids]}) => ids.length,
          ),
          row_height_min: prop_row_height_min.name(),
          curtain_kind: $me_atom2_prop([prop_theme.name()],
            // ({masters: [theme]}) => theme == $nl_theme
            ({masters: [theme]}) => theme == $me_theme.light ? 'white' : 'black',
          ),
          // () => 'white',
          header_height: () => 0,
          provider_tag: $me_atom2_prop([],
            ({atom}) => atom.name()
          ),
          rec_idx_selected: $me_atom2_prop<number[], [string|Set<string>|Map<string,null>, string[]]>(
            [prop_value.name(), prop_option_ids.name()],
            ({masters: [value, ids]}) => (
              typeof value == 'string' ?
                [ids.indexOf(value as string)] :
              value instanceof Set ?
                [...(value as Set<string>)].map((id: string) => ids.indexOf(id)) :
              value instanceof Map ?
                [...(value as Map<string, null>)].map(([id]) => ids.indexOf(id)) :
                []
            ).filter((idx: number) => ~idx)
          ),
          row_i_selected: $me_atom2_prop<number[], [number[], number, number, number, number, number]>(['.rec_idx_selected', '.row_i_min', '.row_i_max', '.row_count', '.visible_idx_min', '.visible_idx_max'],
            ({masters: [idx, row_i_min, row_i_max, row_count, visible_idx_min, visible_idx_max]}) => {
              return idx.map((idx: number) => {
                if (idx < 0) return -1
                if (row_i_max < 0) return -1
                if (!(visible_idx_min <= idx && idx <= visible_idx_max)) return -1
                return (row_i_min + (idx - visible_idx_min)) % row_count
              }).filter((idx: number) => ~idx)
            },
            // ({val}) => console.log(val)
          ),

          header_content: () => <$me_atom2_elem_cnf_intf>{},
          row_content: $me_atom2_prop<$me_atom2_elem_cnf_intf>(
            {keys: ['.row_i']},
            ({key: [row_i]}) => <$me_atom2_elem_cnf_intf>{
              prop: {
                isTouch: () => isTouch,
                row_i: () => row_i,
                rec_idx: $me_atom2_prop(
                  $me_atom2_prop_masters(
                    ['<<.row_i_min', '<<.row_i_max'],
                    ({masters: [row_i_min, row_i_max]}) => $me_list_row_i_out_of_range_is(+row_i, row_i_min, row_i_max) ? [] : [`<<.rec_idx[${row_i}]`],
                  ),
                  ({len, masters: [rec_idx]}) => !len ? -1 : rec_idx
                ),
                id: $me_atom2_prop([prop_option_ids.name(), `.rec_idx`, '<<.rec_count'],
                  ({masters: [ids, idx, rec_count]}) => {
                    return !~idx || idx >= rec_count ? '' : ids[idx]
                  },
                ),
                isSelected: $me_atom2_prop(['<<.row_i_selected'],
                  ({masters: [row_i_selected]}) => !!~row_i_selected.indexOf(+row_i),
                  // ({val}) => console.log({row_i, val})
                ),
                rec_count: '<<.rec_count',

                '#cursor': () => 'pointer',
                '#zIndex': $me_atom2_prop(['<.#zIndex'],
                  ({masters: [zIndex]}) => zIndex + 1,
                ),
                row_cursor_src: $me_atom2_prop<string>(['.isTouch', '.#isHover', '.row_i'],
                  ({masters: [isTouch, isHover, row_i]}) => isTouch || !isHover ? '' : row_i,
                  ({atom, val}) => {
                    $nl_picker_cursor({origin: atom, val: val})
                  },
                ),
              },
              event: {
                clickOrTap: () => {
                  const value = prop_value.value()
                  const id = a<string>('.id')
                  if (value instanceof Set) {
                    const ss = value as Set<string>
                    if (ss.has(id)) {
                      ss.delete(id)
                    } else {
                      ss.add(id)
                    }
                    prop_value.value(ss, true)
                  } else if (value instanceof Map) {
                    const ss = value as Map<string, null>
                    if (ss.has(id)) {
                      ss.delete(id)
                    } else {
                      ss.set(id, null)
                    }
                    prop_value.value(ss, true)
                  } else {
                    prop_value.value(id)
                    prop_isDropdown.value(false)
                  }
                  return true
                },
              },
              style: {
                background: () => 'transparent',
              },
              control: {
                label: () => <$me_atom2_control_cnf_intf>{
                  base: $me_label,
                  prop: {
                    fontSize: prop_itemFontSize.name(),
                    '#width': '<.#width',
                    '#height': '<.#height',
                    alignVer: () => $me_align.center,
                    alignHor: $me_atom2_prop(['<.isTouch'],
                      ({masters: [isTouch]}) => isTouch ? $me_align.center : $me_align.left,
                    ),
                    paddingLeft: $me_atom2_prop([prop_value.name(), '<.isTouch', prop_itemFontSize.name()],
                      ({masters: [value, isTouch, fontSize]}) =>
                        isTouch ? 0 :
                        !(value instanceof Set || value instanceof Map) ? 8 :
                        2 * fontSize,
                    ),
                    '#ofsVer': $me_atom2_prop([
                      `<.rec_idx`,
                      '<.rec_count',
                      prop_itemMarginTopFirst.name(),
                      prop_itemMarginBottomLast.name(),
                      prop_itemMarginVer.name(),
                    ],
                      ({len, masters: [rec_idx, rec_count, itemMarginTopFirst, itemMarginBottomLast, itemMarginVer]}) => {
                        if (!~rec_idx) return 0
                        const result = (
                          !rec_idx ? +(itemMarginTopFirst - itemMarginVer) / 2 :
                          rec_idx == rec_count - 1 ? -(itemMarginBottomLast - itemMarginVer) / 2:
                          0
                        )
                        return result
                      },
                    ),
                    paddingRight: () => 8,
                    text: $me_atom2_prop([`<.rec_idx`, prop_options.name()],
                      ({masters: [rec_idx, options]}) => {
                        if (!~rec_idx) return ''
                        const id = Object.keys(options)[rec_idx]
                        return options[id].caption || id
                      }
                    ),
                    isSelected: '<.isSelected',
                  },
                  render: p => {
                    const value = prop_value.value()
                    if (!(
                      (value instanceof Set || value instanceof Map) &&
                      a<boolean>('.isSelected')
                    )) return
                    const fontSize = a('.fontSize')
                    const h = fontSize * .9
                    const w = h * 1.2
                    const ctxHeight = h * p.pixelRatio
                    const ctxWidth = w * p.pixelRatio
                    const lineWidth = 1
                    const rec_idx = a(`<.rec_idx`)
                    const rec_count = a('<.rec_count')
                    const itemMarginTopFirst = prop_itemMarginTopFirst.value()
                    const itemMarginBottomLast = prop_itemMarginBottomLast.value()
                    const itemMarginVer = prop_itemMarginVer.value()
                    const ofsVer = (a('.#height') - h) / 2
                    const ofsHor = a('<.isTouch') ? 20 : (2 * fontSize - w) / 2
                    $me_atom2_ctx_check({
                      ctx: p.ctx,
                      ctxLeft: p.ctxRect.left + ofsHor * p.pixelRatio,
                      ctxTop: p.ctxRect.top + ofsVer * p.pixelRatio,
                      ctxWidth,
                      ctxHeight,
                      stroke: {
                        style: a<string>('.colorText'),
                        ctxWidth: lineWidth * p.pixelRatio,
                      },
                      lambda: 0.38,
                      mu: 0.6,
                    })
                  },
                }
              },
            },
          ),
          row_cursor: () => '',
          '#order': () => ['row', 'cursor'],
        },
        elem: {
          header: () => null as $me_atom2_elem_cnf_intf,
          cursor: isTouch ?
            $me_atom2_prop<$me_atom2_elem_cnf_intf, [Map<string,null>|Set<string>|string, number[], number[]]>(
              [prop_value.name(), '.row_i_selected', '.rec_idx_selected'],
              ({masters: [value, row_i_selected, rec_idx_selected]}) =>
                value instanceof Set || value instanceof Map || row_i_selected.length != 1 ? null :
                <$me_atom2_elem_cnf_intf>{
                  prop: {
                    '#height': '<.row_height_min',
                    '#ofsVer':
                      rec_idx_selected[0] ?
                      `<.row_top[${row_i_selected[0]}]` :
                      $me_atom2_prop([`<.row_top[${row_i_selected[0]}]`, prop_itemMarginTopFirst.name()],
                        $me_atom2_prop_compute_fn_sum()
                      ),
                  },
                  style: {
                    borderTop: () => '1px solid #D5D5D3',
                    borderBottom: () => '1px solid #D5D5D3',
                    boxSizing: () => 'border-box',
                  },
                },
            ) :
            () => <$me_atom2_elem_cnf_intf>{
              prop: {
                '#hidden': $me_atom2_prop<boolean>(['<.row_cursor'],
                  ({masters: [row_i]}) => !row_i
                ),
                '#ofsHor': () => 3,
                '#ofsVer': $me_atom2_prop(
                  $me_atom2_prop_masters(['<.row_cursor'],
                    ({masters: [row_i]}) => !row_i ? [] : [`<.row_top[${row_i}]`, `<.rec_idx[${row_i}]`]
                  ),
                  ({len, masters: [top, rec_idx]}) =>
                    !len ? null : rec_idx ? top : 3,
                ),
                '#height': $me_atom2_prop(
                  $me_atom2_prop_masters(['<.row_cursor'],
                    ({masters: [row_i]}) => !row_i ? [] : [`<.row_height[${row_i}]`, `<.rec_idx[${row_i}]`, '<.rec_count']
                  ),
                  ({len, masters: [val, rec_idx, rec_count]}) =>
                    !len ? null : !rec_idx ? val - 3 : rec_idx < rec_count - 1 ? val : val - 6,
                ),
                '#width': $me_atom2_prop(['<.#width', '.#ofsHor'],
                  ({masters: [width, ofsHor ]}) =>
                  width - 2 * ofsHor - 2,
                ),
              },
              style: {
                boxShadow: () => '0 1px 6px 0 rgba(0, 0, 0, 0.5)',
                pointerEvents: () => 'none',
              },
            },
        },
        style: {
          background: $me_atom2_prop([prop_theme.name()],
            ({masters: [theme]}) => theme == $me_theme.light ? '#fcfcfd' : '#878f9b',
          ),
          boxShadow: () => '0 8px 12px 0 rgba(0, 0, 0, 0.5)',
        },
        event: {
          mousedown: p => p.isInRect(p.event.clientX, p.event.clientY),
          touchstart: p => p.isInRect(p.event.touches[0].clientX, p.event.touches[0].clientY),
        },
      }
      return true
    }
    return false
  },

  prop: {
    options: $me_atom2_prop_abstract<$me_options>(),
    value: $me_atom2_prop<string|Set<string>|Map<string, null>>(['.option_ids'],
      ({masters: [ids]}) => ids[0],
    ),
    dropdownCountMax: $me_atom2_prop(['.isTouch'],
      ({masters: [isTouch]}) => isTouch ? 9 : Infinity,
    ),
    none: () => '  ',

    option_ids: $me_atom2_prop_keys(['.options']),
    '#cursor': () => 'pointer',
    '#zIndex': $me_atom2_prop(['<.#zIndex'],
      ({masters: [zIndex]}) => zIndex + 1,
    ),
    fontSize: $me_atom2_prop(['.em'],
      $me_atom2_prop_compute_fn_mul(14 / 16)
    ),
    dropdownWidth: $me_atom2_prop(
      $me_atom2_prop_masters(['.isTouch'],
        ({masters: [isTouch]}) => isTouch ? [] : ['.dropdownTargetClientRect']
      ),
      ({len, masters: [clientRect]}) =>
        !len ? 374 : clientRect.right - clientRect.left,
    ),
    dropdownHeight: $me_atom2_prop(
      $me_atom2_prop_masters(['.isTouch'],
        ({masters: [isTouch]}) =>
          (isTouch ?
            ['.row_height_min'] :
            ['/.#viewportHeight', '.dropdownTargetClientRect', '.row_height_min']
          )
          .concat([
            '.option_ids',
            '.dropdownCountMax',
            '.itemMarginTopFirst',
            '.itemMarginBottomLast',
            '.itemMarginVer',
          ]),
      ),
      ({len, masters}) => {
        if (len <= 7) {
          const [row_height_min, option_ids, maxDropdownCount, itemMarginTopFirst, itemMarginBottomLast, itemMarginVer] = masters
          const rec_count = option_ids.length
          const max = maxDropdownCount
          const result = rec_count > max ?
            max * row_height_min :
            rec_count * row_height_min + itemMarginTopFirst + itemMarginBottomLast - 2 * itemMarginVer
          return result
        } else {
          const minMargin = 10
          const [viewportHeight, clientRect, row_height_min, option_ids, maxDropdownCount, itemMarginTopFirst, itemMarginBottomLast, itemMarginVer] = masters
          const correction = (itemMarginTopFirst - itemMarginVer) + (itemMarginBottomLast - itemMarginVer)
          const result =
            Math.max(0, Math.min(
              option_ids.length,
              maxDropdownCount,
              Math.floor((viewportHeight - clientRect.bottom - correction - minMargin) / row_height_min)
            )) * row_height_min + correction
          return result
        }
      },
    ),
    itemFontSize: $me_atom2_prop(
      $me_atom2_prop_masters(['.isTouch'],
        ({masters: [isTouch]}) => ['.isTouch', isTouch ? '.row_height_min' : '.em']
      ),
      ({masters: [isTouch, val]}) => val * (isTouch ? 22/38 : 14/16),
    ),
    itemMarginTopFirst: $me_atom2_prop(['.isTouch'],
      ({masters: [isTouch]}) => isTouch ? 16 : 4,
    ),
    itemMarginBottomLast: $me_atom2_prop(['.isTouch'],
      ({masters: [isTouch]}) => isTouch ? 16 : 4,
    ),
    itemMarginVer: () => 0,
    row_height_min: $me_atom2_prop(['.isTouch'],
      ({masters: [isTouch]}) => isTouch ? 38 : 24,
    ),
  },
  style: {
    borderRadius: () => 3,
    border: $me_atom2_prop(['.isDropdown', '/.theme'],
      ({masters: [isDropdown, theme]}) =>
        (
          !isDropdown ?
          (theme == $me_theme.light ? 'solid 1px #bdc3d1' : 'solid 1px #d8dce3') :
          (theme == $me_theme.light ? 'solid 1px #313745' : 'solid 1px white')
        )
    ),
    boxSizing: () => 'border-box',
    background: $me_atom2_prop(['.isDropdown', '/.theme'],
      ({masters: [isDropdown, theme]}) =>
        (
          !isDropdown ?
          (theme == $me_theme.light ? '#fcfcfd' : '#878f9b') :
          (theme == $me_theme.light ? '#fcfcfd' : '#666f7f')
        )
    ),
    userSelect: () => 'none',
  },
  elem: {
    triangle: () => $nl_triangle,
    text: () => <$me_atom2_elem_cnf_intf>{
      prop: {
        '#ofsHor': () => 8,
        '#width': $me_atom2_prop(['<.#width', '.#ofsHor', '.#ofsHor', '<@triangle.#width', '<@triangle.#ofsHor'],
          $me_atom2_prop_compute_fn_diff()
        ),
      },
      control: {
        label: () => <$me_atom2_control_cnf_intf>{
          base: $me_label,
          prop: {
            '#height': '<.#height',
            '#width': '<.#width',
            text: $me_atom2_prop<string, [Map<string, null>|Set<string>|string, $me_options, string[], string, number, number, CanvasRenderingContext2D]>(
              ['<<.value', '<<.options', '<<.option_ids', '<<.none', '.#width', '/.#pixelRatio', '.#ctx'],
              ({masters: [value, options, option_ids, none, width, pixelRatio, ctx]}) => {
                $me_atom2_control.font_prepare(ctx, pixelRatio)
                const ctxWidth = width * pixelRatio
                let result = none
                const ids: string[] =
                  typeof value == 'string' ? [value as string] :
                  value instanceof Set ? [...(value as Set<string>)].sort((valA, valB) => option_ids.indexOf(valA) - option_ids.indexOf(valB)) :
                  value instanceof Map ? [...(value as Map<string, null>)].map(([id, _]) => id) :
                  []
                // console.log({ctxWidth, width})
                if (ids.length) {
                  let i = 0
                  result = $me_option_caption_text(ids[i], options)
                  while (i + 1 < ids.length && ctx.measureText(result + '   ' + (ids.length - (i + 1))).width < ctxWidth) {
                    let text = result + ', ' + $me_option_caption_text(ids[i + 1], options)
                    if (
                      ctx.measureText(
                        text + (ids.length <= i + 2 ? '' : '   ' + (ids.length - (i + 2)))
                      ).width >= ctxWidth
                    ) {
                      break
                    } else {
                      // console.warn({ctxWidth, width},
                      //   text + (ids.length <= i + 2 ? '' : '   ' + (ids.length - (i + 2))),
                      //   ctx.measureText(
                      //     text + (ids.length <= i + 2 ? '' : '   ' + (ids.length - (i + 2)))
                      //   ).width,
                      //   ctx.font
                      // )
                      result = text
                      i++
                    }
                  }
                  if (i + 1 < ids.length) result += '   ' + (ids.length - (i + 1))
                }
                return result
              },
            ),
            fontSize: '<<.fontSize',
            alignVer: () => $me_align.center,
          },
        },
      },
    },
  },
}

export const $nl_picker_cursor = $me_atom2_async_multi_origin<string, $me_atom2<string, any>>({
  default: '',
  raf_order: 100,
  flush: (row_i: string, prev: string, _value) => {
    (_value.origin.by_path_s('<<.row_cursor') as $me_atom2<string>).value(row_i)
  },
})

// ==========================================================================
}
