export const $nl_search_panel_result_cols = <{
  [id: string]: {
    caption?: string,
    width: number,
    fld?: string[],
    align?: $me_align,
    fn?: any,
  },
}>{

  'guid': {
    width: 150,
    //hidden: true,
    fld: ['guid'],
    fn: (guid: string) => guid,
  },
  photo: {
    caption: '',
    width: 37,
  },
  '': {
    align: $me_align.center,
    width: 42,
    fld: ['total_room_count', 'offer_room_count'],
    fn: (total_room_count: number, offer_room_count: number) =>
      (!offer_room_count ? '' : offer_room_count + '/') +
      (total_room_count == null ? '?' : total_room_count)
  },
  '/': {
    width: 170,
    fld: ['geo_cache_subway_station_name_1'],
    fn: (geo_cache_subway_station_name_1: string) => geo_cache_subway_station_name_1,
  },
  ' ': {
    align: $me_align.center,
    width: 50,
    fld: ['walking_access_1', 'transport_access_1'],
    fn: (walking_access_1: number, transport_access_1: number) =>
      walking_access_1 ? walking_access_1 + '' :
      transport_access_1 ? transport_access_1 + '' :
      '',
  },
  '': {
    width: 220,
    fld: ['geo_cache_street_name', 'geo_cache_building_name'],
    fn: (geo_cache_street_name: string, geo_cache_building_name: string) =>
      geo_cache_street_name ? (
        !geo_cache_building_name ? geo_cache_street_name :
        geo_cache_street_name + ', ' + geo_cache_building_name
      ) : geo_cache_building_name ? geo_cache_building_name : ''
  },
  '': {
    width: 64,
    align: $me_align.center,
    fld: ['storey', 'storeys_count', 'walls_material_type_id'],
    fn: (storey: number, storeys_count: number, walls_material_type_id: number) =>
      (!storey ? '?' : storey) + '/' +
      (!storeys_count ? '?' : storeys_count) + ' ' +
      dic_fld_value('walls_material_type', walls_material_type_id, 'code', '?')
  },
  '': {
    width: 38,
    align: $me_align.center,
    fld: ['balcony_type_id'],
    fn: (type_id: number) => dic_fld_value('balcony_type', type_id, 'code', '?'),
  },
  '': {
    width: 38,
    fld: ['water_closet_type_id'],
    fn: (type_id: number) => dic_fld_value('water_closet_type', type_id, 'code', '?'),
  },
  '': {
    width: 38,
    fld: ['parking_type_id'],
    fn: (type_id: number) => dic_fld_value('parking_type', type_id, 'code', '?'),
  },
  '': {
    width: 112,
    fld: ['territory_type_id'],
    fn: (type_id: number) => dic_fld_value('territory_type', type_id, 'code', '?'),
  },
  '': {
    width: 41,
    fld: ['window_overlook_type_id'],
    fn: (type_id: number) => dic_fld_value('window_overlook_type', type_id, 'code', '?'),
  },
  '': {
    width: 92,
    fld: ['apartment_condition_type_id'],
    fn: (type_id: number) => dic_fld_value('apartment_condition_type', type_id, 'code', '?'),
  },
  '': {
    width: 41,
    fld: ['elevator_type_id'],
    fn: (type_id: number) => dic_fld_value('elevator_type', type_id, 'code', '?'),
  },
  '': {
    align: $me_align.center,
    width: 130,
    fld: ['total_square', 'life_square', 'kitchen_square'],
    fn: (total_square: string, life_square: string, kitchen_square: string) =>
      (total_square || '?') + '/' + (life_square || '?') + '/' + (kitchen_square || '?'),
  },
  ' ': {
    align: $me_align.center,
    width: 138,
    fld: ['square_explication'],
    fn: (square_explication: string) =>
      square_explication == null ? '?' : square_explication,
  },
  '': {
    width: 41,
  },
  '': {
    align: $me_align.right,
    caption: ' ',
    width: 96,
    fld: ['price_rub'],
    fn: (val: number) => {
      var x = (val + '').split('.')
      var x1 = x[0]
      var x2 = x.length > 1 ? '.' + x[1] : ''
      var rgx = /(\d+)(\d{3})/
      while (rgx.test(x1)) x1 = x1.replace(rgx, '$1' + ' ' + '$2')
      return x1 + x2
    },
  },
  ', $': {
    hidden: true,
    align: $me_align.right,
    caption: ' $',
    width: 96,
    fld: ['price_usd'],
    fn: (val: number) => {
      var x = (val + '').split('.')
      var x1 = x[0]
      var x2 = x.length > 1 ? '.' + x[1] : ''
      var rgx = /(\d+)(\d{3})/
      while (rgx.test(x1)) x1 = x1.replace(rgx, '$1' + ' ' + '$2')
      return x1 + x2
    },
  },
  ', ': {
    hidden: true,
    align: $me_align.right,
    caption: ' ',
    width: 96,
    fld: ['price_eur'],
    fn: (val: number) => {
      var x = (val + '').split('.')
      var x1 = x[0]
      var x2 = x.length > 1 ? '.' + x[1] : ''
      var rgx = /(\d+)(\d{3})/
      while (rgx.test(x1)) x1 = x1.replace(rgx, '$1' + ' ' + '$2')
      return x1 + x2
    },
  },
  '  ..': {
    align: $me_align.right,
    caption: '/',
    width: 93,
    fld: ['meter_price_rub'],
    fn: (meter_price_rub: number) => {
      var x = (Math.round(meter_price_rub) + '').split('.')
      var x1 = x[0]
      var x2 = x.length > 1 ? '.' + x[1] : ''
      var rgx = /(\d+)(\d{3})/
      while (rgx.test(x1)) x1 = x1.replace(rgx, '$1' + ' ' + '$2')
      return x1 + x2
    },
  },
  ' $  ..': {
    hidden: true,
    align: $me_align.right,
    caption: '$/',
    width: 93,
    fld: ['meter_price_usd'],
    fn: (val: number) => {
      var x = (Math.round(val) + '').split('.')
      var x1 = x[0]
      var x2 = x.length > 1 ? '.' + x[1] : ''
      var rgx = /(\d+)(\d{3})/
      while (rgx.test(x1)) x1 = x1.replace(rgx, '$1' + ' ' + '$2')
      return x1 + x2
    },
  },
  '   ..': {
    hidden: true,
    align: $me_align.right,
    caption: '/',
    width: 93,
    fld: ['meter_price_eur'],
    fn: (val: number) => {
      var x = (Math.round(val) + '').split('.')
      var x1 = x[0]
      var x2 = x.length > 1 ? '.' + x[1] : ''
      var rgx = /(\d+)(\d{3})/
      while (rgx.test(x1)) x1 = x1.replace(rgx, '$1' + ' ' + '$2')
      return x1 + x2
    },
  },
  '': {
    align: $me_align.right,
    width: 85,
    fld: ['pub_datetime'],
    fn: (val: number) => {
      // new $mol_time_moment(pub_datetime).toString('DD.MM.YYYY')
      const dt = new Date(val)
      return dt.getDate() + '.' + ((dt.getMonth() + 1 + '') as any).padStart(2, '0') + '.' + dt.getFullYear()
    },
  },
  ' ': {
    hidden: true,
    align: $me_align.right,
    width: 85,
    fld: ['creation_datetime'],
    fn: (val: number) => {
      // new $mol_time_moment(pub_datetime).toString('DD.MM.YYYY')
      const dt = new Date(val)
      return dt.getDate() + '.' + ((dt.getMonth() + 1 + '') as any).padStart(2, '0') + '.' + dt.getFullYear()
    },
  },
  ' ': {
    hidden: true,
    align: $me_align.center,
    width: 85,
    fld: ['built_year'],
    fn: (val: number) => {
      return val == null ? '?' : val
      // new $mol_time_moment(pub_datetime).toString('DD.MM.YYYY')
      // const dt = new Date(pub_datetime)
      // return dt.getDate() + '.' + ((dt.getMonth() + 1 + '') as any).padStart(2, '0') + '.' + dt.getFullYear()
    },
  },
  '': {
    // caption: '/',
    width: 140,
    fld:  ['broker', 'media_name'],
    fn: (broker: any, media_name: string) =>
      broker && broker.short_name ? broker.short_name : media_name,
  },
  '': {
    // caption: '/',
    width: 260,
    fld: ['phone_list'],
    fn: (phone_list: string) =>
      !phone_list ? '' : phone_list
        .split(/\D+/)
        .map(phone => '8-' + phone.slice(1, 4) + '-' + phone.slice(4, 7) + '-' + phone.slice(7,9) + '-' + phone.slice(9, 11)).join(', ')
  },
}

const _dics = new Map<string, any>()
function dic_fld_value(dic_name: string, id: number, fld: string, default_value?: any) : any {
  let result : any
  let dic = _dics.get(dic_name)
  if (!dic) {
    const dic_def = DICTIONARIES[dic_name]
    if (dic_def) {
      dic = new Map<number, any>(dic_def.map((item: any) => [item.id, item]))
      _dics.set(dic_name, dic)
    }
  }
  if (dic && dic.has(id)) {
    const value = dic.get(id)
    if (value) result = value[fld]
  }
  if (result === void 0 && default_value !== void 0) result = default_value
  return result
}

