const grid_wheel = (deltaX: number) => {
  const prop_ofsHor = a.get('.ofsHor') as $me_atom2
  const ofsHor = prop_ofsHor.value() - deltaX
  const [min, max] = ofsHor_min_max(a('.col_fixed_width'), a('.col_width_sum_actual'), a('.#width'))
  if (
    deltaX > 0 && ofsHor >= min ||
    deltaX < 0 && ofsHor <= max
  ) {
    prop_ofsHor.value(ofsHor)
  } else if (deltaX) {
    $me_ribbon_effect({
      id: a.curr.name(),
      adjust: '.ofsHor',
      from: '.ofsHor',
      to: ofsHor > max ? max : min,
      delta: deltaX,
      fromBack: deltaX < 0,
    })
  }
  return true
}

function ofsHor_min_max(
  col_fixed_width: number,
  col_width_sum_actual: number,
  width: number,
) {
  const max = col_fixed_width
  const min = Math.min(max, width - col_width_sum_actual)
  return [min, max]
}

function ofsHor_adjusted(
  ofsHor: number,
  min: number,
  max: number,
) {
  return Math.min(max, Math.max(min, ofsHor))
}

const get_row_open = (idx: number, _provider: $me_atom2_path, provider_tag: string) => {
  return a.dispatch<{idx: number, val?: boolean, tag: string}>(_provider, 'get_row_open', {idx, tag: provider_tag}).val
}

const set_row_open = (idx: number, _provider: $me_atom2_path, provider_tag: string, val: number) => {
  a.dispatch(_provider, 'set_row_open', {idx, val, tag: provider_tag})
}

