const colResizer = <$me_atom2_elem_cnf_intf>{
  node: 'img',
  prop: {
    '#width': () => 27,
    '#height': () => 50,
    '#cursor': () => 'col-resize',
    '#zIndex': () => 5,
    start_clientX: () => -1,
    start_width_actual: () => -1,
    start_width: () => -1,
    start_ofsHor: () => -1,
    '#alignHor': $me_atom2_prop(['.isRight'],
      ({masters: [isRight]}) => !isRight ? $me_align.left : $me_align.right,
    ),
    '#ofsHor': () => -19,
    '#ofsVer': () => -4,
  },
  attr: {
    // src: () => 'assets/stretch@2x.png',
    src: $me_atom2_prop(['/.theme'],
      ({masters: [theme]}) => `assets/stretch-${$me_theme[theme]}@2x.png`,
      // theme == $me_theme.light ? '#F5F8F8' : '#878f9b',
    ),
    draggable: () => false, // https://stackoverflow.com/questions/7439042/css-js-to-prevent-dragging-of-ghost-image/7439078#7439078
  },
  style: {
    boxSizing: () => 'border-box',
    transform: $me_atom2_prop(['.isRight'],
      ({masters: [isRight]}) => !isRight ? '' : 'scaleX(-1)',
    ),
  },
  event: {
    touchstart: p => {
      const touchTolerance = a('/.#touchTolerance')
      const dist = p.distToRect(p.event.touches[0].clientX, p.event.touches[0].clientY)
      if (dist > touchTolerance) return false
      resizeStart(p.event.touches[0].clientX)
      return true
    },
    touchmove: p => {
      const start_clientX = a('.start_clientX')
      if (start_clientX == -1) return false
      resizeDo(p.event.touches[0].clientX)
      return true
    },
    touchend: () => resizeFini(),
    // () => {
    //   if (!resizeStop()) return false
    //   resizeFini()
    //   return true
    // },
    mousedown: p => {
      if (!p.isInRect(p.event.clientX, p.event.clientY)) return false
      resizeStart(p.event.clientX)
      return true
    },
    mousemove: p => {
      const start_clientX = a('.start_clientX')
      const clientX = p.event.clientX
      if (start_clientX == -1) return false
      if (!p.event.buttons
        && start_clientX != clientX // this fixes a bug: mousemove arrives after mousedown
      ) {
        return resizeFini(false) // this handles following case: mousedown, pointer out of window, mouseup, pointer into window
        // return false
      }
      resizeDo(p.event.clientX)
      return true
    },
    mouseup: () => resizeFini(),
    // () => {
    //   if (!resizeStop()) return false
    //   resizeFini()
    //   return true
    // },
  },
}

