namespace $.$$ {
// ============================================================================

export interface $me_list2_pos_intf {
  i: number,
  ofsVer: number,
  ofsHor: number,
}

export interface $me_list2_visible_row_intf {
  i: number,
  top: number,
  bottom: number,
  left: number,
  right: number,
}

export enum $me_list_pos_delta_source_enum {
  none,
  wheel,
  bounce,
  manual,
}

export enum $me_list2_state_enum {
  free = 0,
  bounceTop = 1,
  bounceBottom = -1,
  bounceLeft = 1,
  bounceRight = -1,
}

export interface $me_list_delta_intf {
  deltaY: number,
  deltaX: number,
}

export interface $me_list_pos_delta_intf
  extends $me_list_delta_intf
  {
    sourceX: $me_list_pos_delta_source_enum,
    sourceY: $me_list_pos_delta_source_enum,
    phaseY?: $me_atom2_wheel_phase_enum,
    phaseX?: $me_atom2_wheel_phase_enum,
    session?: number,
  }

interface lastWheel_intf {
  session: number,
  phase: $me_atom2_wheel_phase_enum,
}
export type $me_list2_visible_rows_type = Array<$me_list2_visible_row_intf>
export const $me_list2 = <$me_atom2_elem_cnf_intf>{
  prop: {
    firstRowMarginTop: () => 0,
    lastRowMarginBottom: () => 0,
    rowMarginLeft: () => 0,
    rowMarginRight: () => 0,
    pos: () => <$me_list2_pos_intf>{i: 0, ofsVer: 0, ofsHor: 0},
    isReadyData: () => false,
    needRender: () => true,
    _visible_rows: '^canvas.visible_rows',
    _isBegin: '^canvas.isBegin',
    _isEnd: '^canvas.isEnd',
  },
  style: {
    border: () => '1px solid red',
  },
  dispatch(dispatch_name: string, dispatch_arg: any) {
    if (dispatch_name == 'adjust') {
      a.dispatch('^canvas', dispatch_name, dispatch_arg)
      return true
    }
    return false
  },
  control: {
    canvas: () => <$me_atom2_control_cnf_intf>{
      dispatch(dispatch_name: string, dispatch_arg: any) {
        if (dispatch_name == 'wheel') {
          const {clientX, clientY} = dispatch_arg.event
          const clientRect = a<$me_rect_intf>('.#clientRect')
          if ($me_point_in_rect(clientX, clientY, clientRect)) {
              a.update<$me_list_pos_delta_intf>('.pos_delta',
                val => {
                  const val_new = {...val}
                  let changed = false
                  for (const axis of ['X', 'Y']) {
                    if (a<$me_list2_state_enum>(`.state[${axis}]`) == $me_list2_state_enum.free) {
                      val_new['source' + axis] = $me_list_pos_delta_source_enum.wheel
                      val_new['delta' + axis] = dispatch_arg.event['delta' + axis] + (
                        val['source' + axis] != $me_list_pos_delta_source_enum.wheel ||
                        Math.sign(dispatch_arg.event['delta' + axis])*Math.sign(val['delta' + axis]) > 0 ?
                          0 :
                          val['delta' + axis]
                      )
                      val_new['phase' + axis] = dispatch_arg.event['phase' + axis]
                      val_new.session = dispatch_arg.event.session
                      changed = true
                    }
                  }
                  const result = changed ? val_new : val
                  return result
                }
              )
            dispatch_arg.ret = true
          }
          return true
        } else if (dispatch_name == 'adjust') {
          const {axis, delta, source} = dispatch_arg
          a<$me_list_pos_delta_source_enum>(`.adjustEmitterSource[${axis}]`, source)
          a(`.adjustEmitter[${axis}]`, null)
          a(`.adjustEmitter[${axis}]`, $me_atom2_anim({
            from: 0,
            to: delta,
            fn: ({from, prev, to, easing, progress}) =>
              Math.round(from + (to - from) * easing(progress)),
            duration: a(`.adjustDuration`),
            fini: () => {
              // console.log(`%ctarget: ${delta}, accu: ${a(`.adjustEmiiterAccu[${axis}]`)}`, 'background:green;color:white')
              a(`.state[${axis}]`, $me_list2_state_enum.free)
            },
          }))
          return true
        }
        return false
      },
      event: {
        wheel: p => a.dispatch('', 'wheel', {
          event: p.event,
          ret: false,
        }).ret,
        wheelTouch: p => a.dispatch('', 'wheel', {
          event: p.event,
          ret: false,
        }).ret,
      },
      prop: {
        '#width': '<.#width',
        '#height': '<.#height',
        needRender: $me_atom2_prop(['<.needRender', '.#width', '.#height'],
          () => true,
          ({val}) => {
            a('<.needRender', val)
          },
        ),
      },
      prop_non_render: {
        pos_delta: $me_atom2_prop<$me_list_pos_delta_intf>([],
          () => <$me_list_pos_delta_intf>{deltaY: 0, deltaX: 0},
          ({val}) => {
            if (
              val && (
                (val.sourceX != $me_list_pos_delta_source_enum.none) && val.deltaX ||
                (val.sourceY != $me_list_pos_delta_source_enum.none) && val.deltaY
              )
            ) a('.needRender', true)
          }),
        isReadyData: $me_atom2_prop(['<.isReadyData'],
          null,
          ({val}) => {
            if (val) a('.needRender', true)
          },
        ),
        pos: $me_atom2_prop_bind('<.pos'),
        isBegin: () => false,
        isEnd: () => false,

        visible_rows: $me_atom2_prop<$me_list2_visible_rows_type>([],
          () => [] as $me_list2_visible_rows_type,
        ),

        sides: () => ['Top', 'Bottom', 'Left', 'Right'],
        visible: $me_atom2_prop<number, [$me_list2_visible_rows_type]>(
          {keys: ['.sides'], masters: ['.visible_rows']},
          ({key: [side], masters: [visible_rows]}) => {
            const sideLower = side.toLowerCase()
            const sideSign = side == 'Left' ? 1 : -1
            const result =
              !(visible_rows && visible_rows.length) ? null as number :
              side == 'Top' ? visible_rows[0].top :
              side == 'Bottom' ? visible_rows[visible_rows.length - 1].bottom :
              visible_rows.reduce((result, item) =>
                result != null && result*sideSign <= item[sideLower]*sideSign ? result :
                  item[sideLower],
                null as number
              )
            return result
          }
        ),

        lastImg: () => null as any,
        last: $me_atom2_prop<HTMLCanvasElement>(['.#width', '.#height', '/.#pixelRatio'],
          ({masters: [width, height, pixelRatio]}) => {
            const result = document.createElement('canvas')
            result.width = width * pixelRatio
            result.height = height * pixelRatio
            return result
          },
        ),
        bounceTimeout: () => 200,
        wheelSessionToSkip: () => null as number,

        adjustDuration: () => 200,
        axis: () => ['X', 'Y'],
        state: $me_atom2_prop({keys: ['.axis']},
          () => $me_list2_state_enum.free,
        ),
        bounceTimer: $me_atom2_prop({keys: ['.axis']},
          () => null as number,
        ),
        lastWheel: $me_atom2_prop({keys: ['.axis']},
          () => null as lastWheel_intf,
        ),
        adjustEmitter: $me_atom2_prop({keys: ['.axis']},
          () => null as any,
          ({key: [axis], val, prev}) => {
            if (val == null) {
              a(`.adjustEmiiterAccu[${axis}]`, 0)
            } else if (prev != null) {
              const delta = val - prev
              if (delta) {
                const pos_delta_new = {...a<$me_list_pos_delta_intf>('.pos_delta')}
                pos_delta_new['source' + axis] = a(`.adjustEmitterSource[${axis}]`)
                pos_delta_new['delta' + axis] = delta
                a('.pos_delta', pos_delta_new)
                a.update(`.adjustEmiiterAccu[${axis}]`, v => v + delta)
              }
            }
          },
        ),
        adjustEmitterSource: $me_atom2_prop<$me_list_pos_delta_source_enum>({keys: ['.axis']},
          () => $me_list_pos_delta_source_enum.none,
        ),
        adjustEmiiterAccu: $me_atom2_prop({keys: ['.axis']},
          () => 0,
        ),
      },
      render: p => {
      const start = performance.now()
        const {ctx, pixelRatio, ctxHeight, ctxWidth} = p

        const pos_delta = a<$me_list_pos_delta_intf>('.pos_delta')
        a<$me_list_pos_delta_intf>('.pos_delta', <$me_list_pos_delta_intf>{
          sourceY: $me_list_pos_delta_source_enum.none,
          sourceX: $me_list_pos_delta_source_enum.none,
          deltaY: 0,
          deltaX: 0,
        })
        const firstRowMarginTop = a('<.firstRowMarginTop')
        const lastRowMarginBottom = a('<.lastRowMarginBottom')
        const rowMarginLeft = a('<.rowMarginLeft')
        const rowMarginRight = a('<.rowMarginRight')
        const skip = {
          X: false,
          Y: false,
        }
        const immedBounce = {
          X: false,
          Y: false,
        }

        for (const axis of ['X', 'Y'])
          if (pos_delta['source' + axis] == $me_list_pos_delta_source_enum.wheel) {

            a.update<lastWheel_intf>(`.lastWheel[${axis}]`, val => {
              immedBounce[axis] =
                val &&
                val.session == pos_delta.session &&
                val.phase == $me_atom2_wheel_phase_enum.accel &&
                // val.phase != $me_atom2_wheel_phase_enum.decel &&
                pos_delta['phase' + axis] == $me_atom2_wheel_phase_enum.decel
            // if (axis == 'X')
            //   if (immedBounce[axis]) {
            //     console.log(`%cisWheelDecelBecame[${axis}]`, 'background:red;color:white', {...val}, {...pos_delta})
            //   } else {
            //     console.log(`%c!immedBounce[${axis}]`, 'background:yellow', {...val}, {...pos_delta})
            //   }
              return {
                session: pos_delta.session,
                phase: pos_delta['phase' + axis],
              }
            })

            const wheelSessionToSkip = a(`.wheelSessionToSkip`)
            skip[axis] =
              a<$me_list2_state_enum>(`.state[${axis}]`) != $me_list2_state_enum.free ||
              (wheelSessionToSkip != null) && (pos_delta.session == wheelSessionToSkip) ||
              axis == 'X' && a('.visible[Left]') == 0 && a('.visible[Right]') <= ctxWidth/pixelRatio ||
              axis == 'Y' && a('.isBegin') && a('.isEnd')

            if (skip[axis]) {
              if (axis == 'X')
                // console.log(`%cskipped, wheelSessionToSkip: ${wheelSessionToSkip}, session: ${pos_delta.session}`, 'background:yellow')
              continue
            }

            let x: number
            let sizeNet: number
            if (axis == 'Y') {
              sizeNet = ctxHeight/pixelRatio - firstRowMarginTop - lastRowMarginBottom
              const r = pos_delta.deltaY + firstRowMarginTop
              x =
                a('.isBegin') && pos_delta.deltaY < 0 ?
                  a('.visible[Top]') - r :
                a('.isEnd') && pos_delta.deltaY > 0 ?
                  sizeNet - a('.visible[Bottom]') + r :
                  null
            } else {
              sizeNet = ctxWidth/pixelRatio - rowMarginLeft - rowMarginRight
              const r = pos_delta.deltaX + rowMarginLeft
              x =
                a('.visible[Left]') >= rowMarginLeft && pos_delta.deltaX < 0 ?
                  a('.visible[Left]') - r :
                a('.visible[Right]') <= ctxWidth/pixelRatio - rowMarginRight && pos_delta.deltaX > 0 ?
                  sizeNet - a('.visible[Right]') + r :
                  null
            }
            if (x) {
              const sizeBounce = .1*sizeNet
              // http://yotx.ru/#!1/3_h/sH@zv7Rgzhf23/aP9gf2ffiCH8r@1vbW9sHexBt3b3D/ZJNOzGzinj8XSL8bh1ebG7v7UPBA==
              const factor = 10//a('/.#isTouch') ? 10 : 20
              const y = 1/(1+factor*x/sizeBounce)
              pos_delta['delta' + axis] *= y

              // TODO: escape multiblunce effect, appearing after following code:
              if (x/sizeBounce >= 1)
                immedBounce[axis] = true
            }


          }

        if (!a<boolean>('.needRender') || skip.X && skip.Y) {
          const start = performance.now()
          const last = a<HTMLCanvasElement>('.last')
          ctx.drawImage(last, 0, 0, last.width, last.height)
          // console.log('drawImage from last', performance.now() - start)
        } else if (a<boolean>('.isReadyData')) {

          for (const axis of ['X', 'Y'])
            if (skip[axis]) pos_delta['delta' + axis] = 0

          const visible_rows: $me_list2_visible_rows_type = []

          const pos = a<$me_list2_pos_intf>('.pos')
          const ofsVer_from = pos.ofsVer || 0
          const ofsVer = ofsVer_from + pos_delta.deltaY
          const ofsHor_from = pos.ofsHor || 0
          const ofsHor = ofsHor_from + pos_delta.deltaX

          const ctxOfsVer = ofsVer * pixelRatio
          const ctxOfsHor = ofsHor * pixelRatio

          const ctxFirstRowMarginTop = firstRowMarginTop * pixelRatio
          const ctxLastRowMarginBottom = lastRowMarginBottom * pixelRatio
          let ctxTop_from = ctxFirstRowMarginTop - ctxOfsVer
          let ctxTop_from_from = ctxTop_from

          let i_from = pos.i
          let i_from_from = i_from

          const ctxRowMarginLeft = a('<.rowMarginLeft') * pixelRatio
          const ctxRowMarginRight = a('<.rowMarginRight') * pixelRatio
          let ctxLeft = ctxRowMarginLeft - ctxOfsHor

          a.dispatch('<', 'beforeItems', {
            ctx,
            pixelRatio,
            ctxHeight,
            ctxWidth,
            ctxFirstRowMarginTop,
            ctxLastRowMarginBottom,
            ctxRowMarginLeft,
            ctxRowMarginRight,
          })
          const dispatch: row_dispatch_fn_type = (i, ctxTop, ctxBottom) =>
            a.dispatch('<', 'item', {
              ctx,
              pixelRatio,
              ctxHeight,
              ctxWidth,
              ctxLeft,
              ctxRight: ctxWidth,
              ctxRowMarginLeft,
              ctxRowMarginRight,
              ctxFirstRowMarginTop,
              ctxLastRowMarginBottom,
              i,
              ctxTop,
              ctxBottom,
            })

          let ctxBottom: number
          let i: number

          let isFirstItem = false
          let ctxTop = ctxTop_from
          // console.log({ctxTop_from})
          if (ctxTop_from > 0) {
            i = i_from
            while (true) {
              const ret = dispatch(i - 1, null, ctxTop)
              if (ret.ctxTop == null) {
                isFirstItem = true
                break
              }
              i--
              ctxTop = ret.ctxTop
              visible_rows.unshift(<$me_list2_visible_row_intf>{
                i,
                top: ctxTop / pixelRatio,
                bottom: ctxBottom / pixelRatio,
                left: ret.ctxLeft / pixelRatio,
                right: ret.ctxRight / pixelRatio,
              })
              if (ctxTop <= ctxFirstRowMarginTop) break
              ctxBottom = ctxTop
            }
          }
          const isBegin = isFirstItem && ctxTop >= ctxFirstRowMarginTop

          let isLastItem = false
          {
            ctxTop = ctxTop_from
            for (i = i_from; true; i++) {
              const ret = dispatch(i, ctxTop, ctxBottom)
              if (ret.ctxBottom == null) {
                isLastItem = true
                break
              }
              ctxBottom = ret.ctxBottom
              if (ctxBottom > ctxFirstRowMarginTop)
                visible_rows.push(<$me_list2_visible_row_intf>{
                  i,
                  top: ctxTop / pixelRatio,
                  bottom: ctxBottom / pixelRatio,
                  left: ret.ctxLeft / pixelRatio,
                  right: ret.ctxRight / pixelRatio,
                })
              if (ctxBottom > ctxHeight) break
              ctxTop = ctxBottom
            }
          }
          const isEnd = isLastItem && ctxBottom <= ctxHeight - ctxLastRowMarginBottom

          a<$me_list2_visible_rows_type>('.visible_rows', visible_rows)
          a<boolean>('.isEnd', isEnd)
          a<boolean>('.isBegin', isBegin)

          if (visible_rows.length) {
            const row_first = visible_rows[0]
            a('.pos', <$me_list2_pos_intf>{
              i: row_first.i,
              ofsVer: Math.round(firstRowMarginTop - row_first.top),
              ofsHor: Math.round((ctxRowMarginLeft - ctxLeft)/pixelRatio)
            })
          }
          a.dispatch('<', 'afterItems', {
            ctx,
            pixelRatio,
            ctxHeight,
            ctxWidth,
            ctxFirstRowMarginTop,
            ctxLastRowMarginBottom,
            ctxRowMarginLeft,
            ctxRowMarginRight,
            isEnd,
            isBegin,
          })

          const start = performance.now()
          const last = a<HTMLCanvasElement>('.last')
          const ctxLast = last.getContext('2d')
          ctxLast.clearRect(0, 0, ctxWidth, ctxHeight)
          ctxLast.drawImage(
            ((a.get('<') as $me_atom2_elem).node as HTMLCanvasElement),
            0, 0, ctxWidth, ctxHeight,
          )
          // console.log('drawImage to last', performance.now() - start)

          // console.log('processed', pos_delta, 'visible_rows',  visible_rows, 'pos', a('.pos'))

          const path = (a.get('') as $me_atom2_ec).path
          for (const axis of ['X', 'Y'])
            bounce({
              axis: axis as 'X'|'Y',
              ctxSize: axis == 'X' ? ctxWidth : ctxHeight,
              pixelRatio,
              path,
              pos_delta,
              immedBounce,
              atom_isBegin: axis == 'X' ? null : a.get('.isBegin') as $me_atom2<boolean>,
              atom_isEnd: axis == 'X' ? null : a.get('.isEnd') as $me_atom2<boolean>,
            })

        }
        a('.needRender', false)
      // console.log('render took', performance.now() - start)
      },
    },
  },
}

// ============================================================================

function bounce(p: {
  axis: 'X'|'Y',
  ctxSize: number,
  pixelRatio: number,
  path: $me_atom2_path,
  pos_delta: $me_list_pos_delta_intf,
  immedBounce: {X: boolean, Y: boolean},
  atom_isBegin?: $me_atom2<boolean>,
  atom_isEnd?: $me_atom2<boolean>,
}) {
  const {axis, ctxSize, pixelRatio, path, pos_delta, immedBounce, atom_isBegin, atom_isEnd} = p
  const atom_visiblePre = a.get(`.visible[${axis == 'X' ? 'Left' : 'Top'}]`) as $me_atom2
  const atom_visiblePost = a.get(`.visible[${axis == 'X' ? 'Right' : 'Bottom'}]`) as $me_atom2
  const atom_state = a.get(`.state[${axis}]`) as $me_atom2<$me_list2_state_enum>
  const marginPre = a(`<.${axis == 'X' ? 'rowMarginLeft' : 'firstRowMarginTop'}`)
  const marginPost = a(`<.${axis == 'X' ? 'rowMarginRight' : 'lastRowMarginBottom'}`)

  const delta_bounce_top_fn = () =>
    Math.max(0, Math.round(atom_visiblePre.value() - marginPre))
  const delta_bounce_bottom_fn = () =>
    Math.min(0,
      Math.max(
        Math.round(atom_visiblePost.value() - (ctxSize/pixelRatio - marginPost)),
        atom_isBegin && !atom_isBegin.value() ? -Infinity : Math.round(atom_visiblePre.value() - marginPre),
      )
    )
  let bounceState: $me_list2_state_enum
  let delta: number
  const state = atom_state.value()
  if (
    (!atom_isBegin || atom_isBegin.value()) &&
    (delta = delta_bounce_top_fn()) * (bounceState = $me_list2_state_enum.bounceTop) > 0
  ) {
    if (state == $me_list2_state_enum.free) {
      do_bounce(axis, delta, delta_bounce_top_fn, bounceState, atom_state, path, immedBounce[axis], pos_delta, atom_isBegin)
    } else if (state != bounceState) {
      do_bounce_helper(axis, delta, bounceState, atom_state, path)
    }
  } else if (
    (!atom_isEnd || atom_isEnd.value()) &&
    state == $me_list2_state_enum.free &&
    (delta = delta_bounce_bottom_fn()) * (bounceState = $me_list2_state_enum.bounceBottom) > 0
  ) {
    do_bounce(axis, delta, delta_bounce_bottom_fn, bounceState, atom_state, path, immedBounce[axis], pos_delta, atom_isEnd)
  }
}

function do_bounce(
  axis: 'X'|'Y',
  delta: number,
  delta_fn: () => number,
  bounceState: $me_list2_state_enum,
  atom_state: $me_atom2<$me_list2_state_enum>,
  path: $me_atom2_path,
  immedBounce: boolean,
  pos_delta: $me_list_pos_delta_intf,
  atom_isEdge?: $me_atom2<boolean>,
) {
  const bounce_fn = () => {
    let delta: number
    if (
      (!atom_isEdge || atom_isEdge.value()) &&
      atom_state.value() == $me_list2_state_enum.free &&
      (delta = delta_fn()) * bounceState > 0
    ) do_bounce_helper(axis, delta, bounceState, atom_state, path)
  }
  if (immedBounce) {
    a('.wheelSessionToSkip', pos_delta.session)
    clearTimeout(a<any>(`.bounceTimer[${axis}]`))
    do_bounce_helper(axis, delta, bounceState, atom_state, path)
  } else {
    a.update<any>(`.bounceTimer[${axis}]`, timer => {
      if (timer) clearTimeout(timer)
      return setTimeout(bounce_fn, a('.bounceTimeout'))
    })
  }
}

function do_bounce_helper(
  axis: 'X'|'Y',
  delta: number,
  state: $me_list2_state_enum,
  atom_state: $me_atom2<$me_list2_state_enum>,
  path: $me_atom2_path,
  duration?: number,
) {
  atom_state.value(state)
  const dispatch_arg = {axis, delta, source: $me_list_pos_delta_source_enum.bounce} as any
  if (duration !== void 0) dispatch_arg.duration = duration
  a.dispatch(path, `adjust`, dispatch_arg)
}

// ============================================================================

type after_reverse_fn_ret_type = {
  i_from: number,
  ctxTop_from: number,
}
type after_reverse_fn_type = (i: number, ctxTop: number) =>
  void | after_reverse_fn_ret_type
type after_fn_type = (i: number, ctxTop: number, ctxBottom: number) =>
  void
type row_dispatch_fn_type = (i: number, ctxTop: number, ctxBottom: number) =>
  row_dispatch_fn_ret_type
type row_dispatch_fn_ret_type = {
  ctxBottom: number,
  ctxTop: number,
  ctxLeft: number,
  ctxRight: number,
}

interface render_helper_ret_type {
  ctxBottom: number,
  isBegin: boolean,
  isEnd: boolean,
  visible_rows: $me_list2_visible_rows_type,
}

// ============================================================================
}

