namespace $.$$ {
// ============================================================================

export const $me_cross = <$me_atom2_elem_cnf_intf>{
  type: '$me_cross',
  base: $me_stylesheet,
  prop: {
  // required:
    size: $me_atom2_prop_abstract(),
    thick: $me_atom2_prop_abstract(),
    color: $me_atom2_prop_abstract<string>(),

  // optional:
    opacity: () => 1,
    opacityHover: () => 1,

  // internal:
    '#width': '.size',
    '#height': '.size',

    styleSheetName: () => 'me_cross',
    styleSheetCommon: $me_atom2_prop<string>(['.styleSheetName'],
      ({masters: [className]}) =>
      // https://codepen.io/brissmyr/pen/egidw
      `
        .${className}:before {
          transform: rotate(45deg);
        }
        .${className}:after {
          transform: rotate(-45deg);
        }
      `
    ),
    styleSheet: $me_atom2_prop<string>(['.className', '.size', '.thick', '.color', '.opacity', '.opacityHover'],
      ({masters: [className, size, thick, color, opacity, opacityHover], atom}) =>
      // https://codepen.io/brissmyr/pen/egidw
      `
        .${className} {
          opacity: ${opacity};
        }
        .${className}:hover {
          opacity: ${opacityHover};
        }
        .${className}:before, .${className}:after {
          position: absolute;
          left: ${(size - thick) / 2}px;
          content: '';
          height: ${size}px;
          width: ${thick}px;
          background-color: ${color};
        }
      `
    ),
  },
}

// ==========================================================================
}
